import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { URLS } from '../app-url.constant';

@Injectable({
  providedIn: 'root',
})
export class PowerbiReportService {
  constructor(private http: HttpClient) {}

  getEmbedReport(reportType: any) {
    let param = {
      reportType: reportType,
    };
    return this.http.get(
      `${URLS.baseApiHostUrl}${URLS.PBIReport.endpoint}${URLS.PBIReport.methods.GetEmbedReport}`,
      { params: param }
    );
  }
}
