import {
  Component,
  ElementRef,
  TemplateRef,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { DatePipe } from '@angular/common';
import { MatDialog } from '@angular/material/dialog';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { commonData } from '../../../shared/commonData';
import { GhgAvoidanceService } from 'src/app/services/manager/ghg-avoidance.service';
import { MessageService } from 'src/app/services/message.service';
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';
import {
  MomentDateAdapter,
  MAT_MOMENT_DATE_ADAPTER_OPTIONS,
} from '@angular/material-moment-adapter';
import { MAT_DATE_LOCALE, MAT_DATE_FORMATS } from '@angular/material/core';
import { MY_FORMATS } from '../../../supervisor/ghg-emission/fuel-table/fuel-table.component';
import { DateAdapter } from '@angular/material/core';
import * as moment from 'moment';
import { ConfirmationDialogComponent } from '../../../shared/confirmation-dialog/confirmation-dialog.component';

export interface classtableHeader {
  Sno: Number;
  effectiveDate: Date;
  dropdowntype: string;
  createdBy: string;
  createdOn: Date;
  groupId: Number;
}

@Component({
  selector: 'app-displacement-configuration',
  templateUrl: './displacement-configuration.component.html',
  styleUrls: ['./displacement-configuration.component.scss'],
  encapsulation: ViewEncapsulation.Emulated,
  providers: [
    {
      provide: DateAdapter,
      useClass: MomentDateAdapter,
      deps: [MAT_DATE_LOCALE, MAT_MOMENT_DATE_ADAPTER_OPTIONS],
    },
    { provide: MAT_DATE_FORMATS, useValue: MY_FORMATS },
  ],
})
export class DisplacementConfigurationComponent {
  @ViewChild('addConfigurationModel') addConfigurationModel!: TemplateRef<any>;
  @ViewChild('showConfigurationDetails')
  showConfigurationDetails!: TemplateRef<any>;
  @ViewChild('serachInput') serachInput: any;

  @ViewChild('dp1') dp1!: ElementRef<any>;
  @ViewChild('material2') material2!: ElementRef<any>;

  displacementForm!: FormGroup;
  pageSize = this._commonData.pageSize;
  currentPage = this._commonData.currentPage;
  AddDialogRef: any;
  fuelArray: any = [];
  fueltypeArray: any = [];
  filteredFuelTypeArray: any = [];
  materialArray: any = [];
  displacementByIdArray: any = [];
  Math: any = Math;
  tableHeaderArray = [
    { name: 'S.No', width: '15%' },
    { name: 'Effective Date', width: '18%' },
    { name: 'Material', width: '18%' },
    { name: 'Maintained By', width: '20%' },
    { name: 'Maintained On', width: '25%' },
    {
      name: '',
      width: '4%',
    },
  ];
  'minDate' = new Date();

  tableData: classtableHeader[] = [];
  tableDataLength: any;

  // tableData: classtableHeader[] = [
  //   {
  //     Sno: 1,
  //     effectiveDate: new Date(),
  //     material: 'Natural Gas',
  //     maintainedBy: 'Admin',
  //     maintainedOn: new Date(),
  //   },
  //   {
  //     Sno: 2,
  //     effectiveDate: new Date(),
  //     material: 'Natural Gas',
  //     maintainedBy: 'Admin',
  //     maintainedOn: new Date(),
  //   },
  //   {
  //     Sno: 3,
  //     effectiveDate: new Date(),
  //     material: 'Natural Gas',
  //     maintainedBy: 'Admin',
  //     maintainedOn: new Date(),
  //   },
  //   {
  //     Sno: 4,
  //     effectiveDate: new Date(),
  //     material: 'Natural Gas',
  //     maintainedBy: 'Admin',
  //     maintainedOn: new Date(),
  //   },
  //   {
  //     Sno: 5,
  //     effectiveDate: new Date(),
  //     material: 'Natural Gas',
  //     maintainedBy: 'Admin',
  //     maintainedOn: new Date(),
  //   },
  // ];
  constructor(
    private dialog: MatDialog,
    private _fb: FormBuilder,
    private _commonData: commonData,
    private _ghgAvoidanceSvc: GhgAvoidanceService,
    private msgService: MessageService,
    private _loader: NgxSpinnerService,
    private _datepipe: DatePipe
  ) {
    this.formInitialise();
    this.getGetAllGHGAvoidanceDisplacementConfig();
    console.log('child displacement working ...');
  }

  formInitialise() {
    this.displacementForm = this._fb.group({
      effectiveDate: ['', [Validators.required]],
      material: ['', Validators.required],
      fuelTable: this._fb.array([this.createFuelTable()]),
    });
  }

  getGetAllGHGAvoidanceDisplacementConfig(): void {
    this._loader.show();
    this._ghgAvoidanceSvc.GetAllGHGAvoidanceDisplacementConfig().subscribe(
      (res: any) => {
        this._loader.hide();
        if (res.data.length > 0) {
          this.tableData = res.data;
          this.tableDataLength = res.data.length;
        } else {
          this.tableData = [];
          this.msgService.errorToast(res.errorMessage);
        }
      },
      (err: any) => {
        this._loader.hide();
        this.tableData = [];
        this.msgService.errorToast(err.error);
      }
    );
  }

  GetAllGHGAvoidanceFuel(): void {
    this._loader.show();
    this.fuelArray = [];
    this._ghgAvoidanceSvc.GetAllGHGAvoidanceFuel().subscribe(
      (res: any) => {
        this._loader.hide();
        if (res.data.length > 0) {
          this.fuelArray = res.data;
        } else {
          this.fuelArray = [];
          this.msgService.errorToast(res.errorMessage);
        }
      },
      (err: any) => {
        this._loader.hide();
        this.fuelArray = [];
        this.msgService.errorToast(err.error);
      }
    );
  }

  GetAllGHGAvoidanceFuelType(value: any, index: any): void {
    this.serachInput.value = '';
    const formGroupAtIndex = (
      this.displacementForm.get('fuelTable') as FormArray
    ).at(index) as FormGroup;

    formGroupAtIndex.controls['fuelTypeId'].reset();
    formGroupAtIndex.controls['percentage'].reset();

    this._ghgAvoidanceSvc
      .GetAllGHGAvoidanceFuelType(Number(value.target.value))
      .subscribe(
        (res: any) => {
          if (res.data.length > 0) {
            this.fueltypeArray[index] = res.data;
            this.filteredFuelTypeArray[index] = [...res.data];
          } else {
            this.fueltypeArray = [];
            this.filteredFuelTypeArray = [];
            this.msgService.errorToast(res.errorMessage);
          }
        },
        (err: any) => {
          this.fueltypeArray = [];
          this.filteredFuelTypeArray = [];
          this.msgService.errorToast(err.error);
        }
      );
  }

  getAllMaterialList() {
    this.materialArray = [];

    this._ghgAvoidanceSvc.GetGhgavoidanceDropdowntype(null).subscribe(
      (res: any) => {
        if (res.data.length > 0) {
          this.materialArray = res.data;
        } else {
          this.materialArray = [];
          this.msgService.errorToast(res.errorMessage);
        }
      },
      (err: any) => {
        this.materialArray = [];
        this.msgService.errorToast(err.error);
      }
    );
  }

  dateSelection(event: any) {
    var material = this.materialArray.find((res: any) => {
      return res.id == this.displacementForm.value.material;
    }); ////find the name of selected material

    var data = this.tableData.find(element => {
      return (
        this._datepipe.transform(
          this.displacementForm.value.effectiveDate,
          'dd-mm-yyyy'
        ) === this._datepipe.transform(element.effectiveDate, 'dd-mm-yyyy') &&
        element.dropdowntype == material.dropdowntype
      );
    });
    if (data) {
      this.msgService.errorToast(
        'this material is already exist with the give effective date'
      );
      this.displacementForm.controls['material'].reset('');
    }
  }

  createFuelTable() {
    return this._fb.group({
      fuelId: [null, Validators.required],
      fuelTypeId: [null, Validators.required],
      percentage: ['', Validators.required],
    });
  }

  addItem(): void {
    if (this.displacementForm.invalid) {
      this.msgService.errorToast('Please fill all the required fields');
      this.displacementForm.markAllAsTouched();
      return;
    } else {
      var total: number = 0;
      this.displacementForm.value.fuelTable.forEach((res: any) => {
        total += Number(res.percentage);
      });

      if (total < 100) {
        var items = this.displacementForm.get('fuelTable') as FormArray;
        items.push(this.createFuelTable());
      } else {
        this.msgService.errorToast(
          'percentage total is already 100 you cant add more rows'
        );
      }
    }
  }

  deleteRow(index: number): void {
    const arr = this.displacementForm.get('fuelTable') as FormArray;
    arr.removeAt(index);
  }

  get getControls() {
    return (this.displacementForm.get('fuelTable') as FormArray).controls;
  }

  addConfiguration() {
    this.GetAllGHGAvoidanceFuel();
    this.getAllMaterialList();
    this.AddDialogRef = this.dialog.open(this.addConfigurationModel, {
      width: '70%',
      height: '80%',
    });

    this.AddDialogRef.afterClosed().subscribe((res: any) => {
      this.displacementForm.reset();
      const arr: any = this.displacementForm.controls['fuelTable'];
      arr.clear();
      arr.push(this.createFuelTable());
      this.fueltypeArray = [];
      this.filteredFuelTypeArray = [];
    });
  }

  getDisplacementById(data: any) {
    this.displacementByIdArray = [];
    this._ghgAvoidanceSvc
      .GetGHGAvoidanceDisplacementConfigByGroupId(data.groupId)
      .subscribe((res: any) => {
        this.displacementByIdArray = {
          data: data,
          res: res.data,
        };

        this.dialog.open(this.showConfigurationDetails, {
          width: '70%',
        });
      });
  }

  save() {
    if (this.displacementForm.invalid) {
      this.displacementForm.markAllAsTouched();
      this.msgService.errorToast('Please fill all the required details');
    } else {
      var total: number = 0;
      this.displacementForm.value.fuelTable.forEach((res: any) => {
        total += Number(res.percentage);
      });

      if (total < 100) {
        this.msgService.errorToast('Total percentage cant be less than 100%');
      } else if (total > 100) {
        this.msgService.errorToast('Total percentage cant be more than 100%');
      } else {
        var dialogRef1 = this.dialog.open(ConfirmationDialogComponent, {
          width: '24.514vw', //353px
          height: '10.694vw', //154px
          data: {
            header: 'Save ?',
            data: { bodyData: `Are you sure, you want to submit the changes?` },
            button: {
              isSubmit: true,
              isCancel: true,
              isDelete: false,
            },
          },
        });
        dialogRef1.disableClose = true;

        dialogRef1.afterClosed().subscribe(result => {
          if (result == 'ok') {
            this._loader.show();
            this._ghgAvoidanceSvc
              .AddGHGAvoidanceDisplacementConfig(this.displacementForm.value)
              .subscribe(
                (res: any) => {
                  this._loader.hide();
                  this.AddDialogRef.close();
                  this.getGetAllGHGAvoidanceDisplacementConfig();
                },
                (err: any) => {
                  this._loader.hide();
                  this.msgService.errorToast(err.error);
                }
              );
          }
        });
      }
    }
  }
}
