  subModuleChange(item: any, index: any, isCheck: boolean) {
    if (isCheck && this.isAllDataSaved()) {
      this.confirmationDialog({ item, index }, 'sub');
    } else {
      if (this.selectedScopeId && this.selectedScopeId != item?.id) {
        this.accordionDataListBasedOnDate = [];
        this.cloneOfAccordionDataListBasedOnDate = [];
        this.scope3AccordionDataListBasedOnDate = [];
        this.cloneOfScope3AccordionDataListBasedOnDate = [];
        this.accordionData = [];
        this.scope3AccordionData = [];
        this.scopeEnable(index); // use to show which scope button is selected

        this.getScopeSubTab(item?.moduleId, item?.id); // use to get Category list
        this.selectedScopeId = item?.id;
        this.selectedScopeName = item?.submodule;
        // this.getListOfMonths(item?.id);
        if (item?.submodule?.includes('Scope-3')) this.scope3ApiMethod();
        this.scope1And2DateOverFlow = false;
        this.scope3DateOverFlow = false;

        // this.selectedDropdownDate = '';
      }
    }
  }

  changeCategory(item: any, index: any, isCheck: boolean) {
    if (isCheck && this.isAllDataSaved()) {
      this.confirmationDialog({ item, index }, 'cat');
    } else {
      this.accordionDataListBasedOnDate =
        this.cloneOfAccordionDataListBasedOnDate;
      this.scope3AccordionDataListBasedOnDate =
        this.cloneOfScope3AccordionDataListBasedOnDate;
      this.accordionData = [];
      this.scope3AccordionData = [];

      this.scopeSubTabEnable(index);
      this.selectedCategoryName = item?.categoryName.split(': ')[1];
      this.selectedCategoryName2 = item?.categoryName.split(': ')[0];
      this.isScope1Cat2 =
        this.selectedScopeName?.includes('Scope-1') &&
        item?.categoryName.split(': ')[0] == 'Category-2'
          ? true
          : false;
      this.getAccordionList(item?.moduleId, item?.submoduleId, item?.id);
      this.getGhgAccordionDataBasedOnDate();
    }
  }

  isAllDataSaved() {
    return this.selectedScopeId != 3
      ? (this.accordionDataListBasedOnDate.length > 0 &&
          this.accordionDataListBasedOnDate.some((ele: any) => !ele.id)) ||
        this.isFieldIsTouchedOrEdited
        ? true
        : false
      : (this.scope3AccordionDataListBasedOnDate.length > 0 &&
            this.scope3AccordionDataListBasedOnDate.some(
              (ele: any) => !ele.id
            )) ||
          this.isFieldIsTouchedOrEdited
        ? true
        : false;
  }

  onSelectFiles(event: any) {
    const file = event?.target?.files[0];
    if (
      file.type ==
        'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' ||
      file.type == 'application/vnd.ms-excel'
    ) {
      const fileSizeInKB = file?.size / 1024;
      if (fileSizeInKB >= 51) {
        return this.messageService.errorToast(
          'Please upload an image less than 50KB'
        );
      }
      this.uploadEmployeeExcel(file, event);
    } else {
      return this.messageService.errorToast('Please upload only excel file');
    }
  }

  uploadEmployeeExcel(file: any, event: any) {
    this.spinner.show();
    let formData = new FormData();
    formData.append('file', file);
    if (this.categoryId == this.commonCatData.scope3Cat7) {
      formData.append('CorporateOfficeId', this.corporateOfficeId);
      formData.append('RegionalOfficeId', this.regionalOfficeId ?? 0);
      formData.append('SiteOfficeId', this.siteOfficeId ?? 0);
    }
    this.ghgService
      .uploadEmployeeExcel(formData, this.categoryId)
      .subscribe((res: any) => {
        if (!res.data) this.messageService.errorToast(res.errorMessage);
        else {
          if (this.categoryId == this.commonCatData.scope3Cat7)
            this.getEmployeeList();
          else if (
            this.categoryId == this.commonCatData.scope3Cat8 ||
            this.categoryId == this.commonCatData.scope3Cat13
          )
            this.getGhgEmissionLeasedAssetList();
          else if (
            this.categoryId == this.commonCatData.scope3Cat10 ||
            this.categoryId == this.commonCatData.scope3Cat11 ||
            this.categoryId == this.commonCatData.scope3Cat12
          )
            this.getGhgEmissionSoldProductList();
          else if (this.categoryId == this.commonCatData.scope3Cat14)
            this.getGhgEmissionFranchiseList();
          else this.getGhgEmissionInvestmentList();
          this.messageService.successToast('File uploaded successfully');
          this.changeDetector.detectChanges();
        }
        this.spinner.hide();
      });
    event.target.value = '';
  }

  downloadExcelFile() {
    this.spinner.show();
    this.ghgService
      .downloadExcelTemplate(this.categoryId)
      .subscribe((res: any) => {
        this.spinner.hide();
        const url = window.URL.createObjectURL(res);
        const anchor = document.createElement('a');
        anchor.href = url;
        anchor.download = this.selectedCategoryName;
        anchor.click();

        // Cleanup (optional):
        window.URL.revokeObjectURL(url);
      });
  }

  downloadScope3RowExcelFile() {
    this.spinner.show();
    this.ghgService
      .downloadRowExcelTemplate(this.categoryId)
      .subscribe((res: any) => {
        this.spinner.hide();
        const url = window.URL.createObjectURL(res);
        const anchor = document.createElement('a');
        anchor.href = url;
        anchor.download = this.selectedCategoryName;
        anchor.click();

        // Cleanup (optional):
        window.URL.revokeObjectURL(url);
      });
  }

