  onRowSelectFiles(event: any) {
    const file = event?.target?.files[0];
    if (
      file.type ==
        'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' ||
      file.type == 'application/vnd.ms-excel'
    ) {
      const fileSizeInKB = file?.size / 1024;
      if (fileSizeInKB >= 51) {
        return this.messageService.errorToast(
          'Please upload an image less than 50KB'
        );
      }
      this.uploadRowExcelFile(file, event);
    } else {
      return this.messageService.errorToast('Please upload only excel file');
    }
  }

  uploadRowExcelFile(file: any, event: any) {
    this.spinner.show();
    let formData: any = new FormData();
    formData.append(
      'requestUserLoginName',
      JSON.parse(localStorage.getItem('userData')!).username
    );
    formData.append('requestId', null);
    formData.append(
      'data.CorporateOfficeId',
      this.corporateOfficeId ? Number(this.corporateOfficeId) : 0
    );
    formData.append(
      'data.RegionalOfficeId',
      this?.regionalOfficeId ? Number(this.regionalOfficeId) : 0
    );
    formData.append(
      'data.SiteOfficeId',
      this?.siteOfficeId ? Number(this.siteOfficeId) : 0
    );
    formData.append('data.ModuleId', this.moduleId);
    formData.append('data.SubModuleId', this.subModuleId);
    formData.append('data.CategoryId', this.categoryId);
    formData.append('data.DropdownTypeId', this.dropdownTypeId);
    formData.append('data.file', file);
    formData.append('data.DataCapturingSlotGroupId', this.selectedDropdownDate);
    formData.append('message', null);
    this.ghgService
      .uploadRowExcelTemplate(this.categoryId, formData)
      .subscribe((res: any) => {
        if (!res.data) this.messageService.errorToast(res.errorMessage);
        else {
          this.messageService.successToast('File uploaded successfully');
          // this.isRowExcelUploaded.emit();
          this.isRowExcelUploaded();
        }
        this.spinner.hide();
      });
    event.target.value = '';
  }

  showingHistory() {
    this.spinner.show();
    this.historyBatchData = [];
    let requestBody: any = {
      requestUserLoginName: JSON.parse(localStorage.getItem('userData')!)
        .username,
      requestId: 'string',
      data: {
        batchId: this.batchDetail?.id,
      },
      message: 'string',
    };
    this.ghgService
      .getEmissionBatchHistory(requestBody)
      .subscribe((res: any) => {
        this.spinner.hide();
        this.historyBatchData = res?.data;
        this.isHistoryEmpty =
          res?.data?.length == 0 || !res?.data ? true : false;
        // this.openHistoryModel();
      });
  }

  openCommentModel(headerData: any) {
    this.commentHeaderName = headerData;
    this.commentDialogRef = this.dialog.open(this.commentModel, {
      width: '50%',
      height: '50%',
    });

    this.commentDialogRef.afterClosed().subscribe((res: any) => {
      this.userComment = '';
      this.clickButtonId = null;
      this.commentDialogRef = null;
      this.commentHeaderName = null;
    });
  }

  openHistoryModel(data: any) {
    this.completeDataForSingleHistory = data;
    this.historyDialogRef = this.dialog.open(this.batchHistoryModel, {
      width: '55%',
      height: '50%',
    });

    this.historyDialogRef.afterClosed().subscribe((res: any) => {
      this.historyDialogRef = null;
      this.completeDataForSingleHistory = null;
    });
  }

  commentRequired(buttonId: any) {
    if (!this.userComment)
      this.messageService.errorToast('Please provide comment.');
    else this.addAndValidateGhg(buttonId);
  }

  showCommentModel(buttonId: any) {
    this.clickButtonId = buttonId;
    this.openCommentModel('Comment');
  }
