import {
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  OnInit,
  Output,
  SimpleChanges,
  ViewChild,
} from '@angular/core';
import {
  MomentDateAdapter,
  MAT_MOMENT_DATE_ADAPTER_OPTIONS,
} from '@angular/material-moment-adapter';
import {
  MAT_DATE_LOCALE,
  MAT_DATE_FORMATS,
  DateAdapter,
} from '@angular/material/core';
import * as moment from 'moment';
import { MY_FORMATS } from '../../ghg-emission/fuel-table/fuel-table.component';
import { NgxSpinnerService } from 'ngx-spinner';
import {
  AbstractControl,
  FormArray,
  FormBuilder,
  FormGroup,
  Validators,
} from '@angular/forms';
import { Subject, debounceTime } from 'rxjs';
import { MessageService } from 'src/app/services/message.service';
import { GhgAvoidanceService } from 'src/app/services/manager/ghg-avoidance.service';
import { DatePipe, DecimalPipe } from '@angular/common';
import { ghgEmissionAvoidanceActionType } from 'src/app/components/shared/commonData';

@Component({
  selector: 'app-input-table',
  templateUrl: './input-table.component.html',
  styleUrls: ['./input-table.component.scss'],
  providers: [
    {
      provide: DateAdapter,
      useClass: MomentDateAdapter,
      deps: [MAT_DATE_LOCALE, MAT_MOMENT_DATE_ADAPTER_OPTIONS],
    },
    { provide: MAT_DATE_FORMATS, useValue: MY_FORMATS },
  ],
})
export class InputTableComponent implements OnInit {
  @Input() accordionData!: any;
  @Input() apiData!: any;
  @Input() unitData!: any;
  @Output() deleteAccordionData = new EventEmitter<any>();
  @Output() updateDataToParent = new EventEmitter<any>();
  @Output() isFieldIsTouched = new EventEmitter<boolean>();
  @ViewChild('scrollBarTable') scrollBarTable!: ElementRef;

  private typingTimer: Subject<any> = new Subject();

  // minDate = moment(new Date());
  // maxDate = moment(new Date());

  minDate: any;
  maxDate: any;

  disableToDate: { [key: number]: Date } = {};
  isDateOverlappingChecking: { [key: number]: boolean } = {};
  avoidanceForm!: FormGroup;
  fuelData: any = [];
  isCheckingError: boolean = false;

  constructor(
    private spinner: NgxSpinnerService,
    private fb: FormBuilder,
    private messageService: MessageService,
    private ghgAvoidanceService: GhgAvoidanceService,
    private _datepipe: DatePipe,
    private changeDetector: ChangeDetectorRef,
    private decimalPipe: DecimalPipe,
    public actionType: ghgEmissionAvoidanceActionType
  ) {}
  // ngOnChanges(changes: SimpleChanges): void {
  //   console.log(changes);
  //   console.log(this.accordionData, 'accordian data');
  // }

  ngOnInit(): void {
    console.log('child works');
    this.isCheckingError = false;
    this.fillDataInForm(this.accordionData);
    this.fuelData = this.accordionData;
    this.typingTimer
      .pipe(debounceTime(900)) // Adjust the debounce time as needed (in milliseconds)
      .subscribe((res: any) => {
        this.getEmissionValue(res);
      });
  }

  addTableColumn() {
    if (this.apiData?.actionDetail?.includes(this.actionType.addAction)) {
      this.spinner.show();
      const formArray = this.avoidanceForm.get('tableData') as FormArray;

      var isTotalZero: boolean = false;
      // for (var i = 0; i < formArray.value.length; i++) {
      //   if (
      //     Number(formArray.value[i].transportValue) == 0 &&
      //     Number(formArray.value[i].commercialValue) == 0 &&
      //     Number(formArray.value[i].industrialValue) == 0 &&
      //     Number(formArray.value[i].householdValue) == 0
      //   ) {
      //     this.spinner.hide();
      //     this.messageService.errorToast("The total of a row can't be 0");
      //     isTotalZero = true;
      //     break;
      //   }
      // }

      if (!isTotalZero) {
        this.completeFuelForms.push(this.createTableRow());
        setTimeout(() => {
          if (this.completeFuelForms?.controls?.length > 5)
            this.scrollBarTable.nativeElement.scrollTop =
              this.scrollBarTable.nativeElement.scrollHeight;
          else window.scrollTo(0, document.body.scrollHeight + 100);
          this.spinner.hide();
        }, 0);
      }
    }
  }

  get completeFuelForms() {
    return this.avoidanceForm?.get('tableData') as FormArray;
  }

  getFormRow(index: any) {
    return this.completeFuelForms.at(index);
  }

  createTableRow() {
    console.log(this.apiData, 'apidatas');

    let data: any = null;
    if (this.fuelData?.length > 0) {
      data = this.fuelData[0];
    }
    return this.fb.group({
      id: 0,
      corporateOfficeId: this.apiData?.corporateOfficeId
        ? this.apiData?.corporateOfficeId
        : null,
      regionalOfficeId: this.apiData?.regionalOfficeId
        ? this.apiData?.regionalOfficeId
        : null,
      siteOfficeId: this.apiData?.siteOfficeId
        ? this.apiData?.siteOfficeId
        : null,
      moduleId: this.apiData?.moduleId ? this.apiData?.moduleId : null,
      subModuleId: this.apiData?.subModuleId ? this.apiData?.subModuleId : null,
      ghgavoidanceCategoryId: 1,
      ghgavoidanceDropdownTypeId: this.apiData?.dropdownTypeId
        ? this.apiData?.dropdownTypeId
        : null,
      dataCapturingSlotGroupId: this.apiData?.dataCapturingSlotGroupId
        ? Number(this.apiData?.dataCapturingSlotGroupId)
        : null,
      sourceCityId: data && data?.sourceCityId ? data?.sourceCityId : null,
      destinationCityId:
        data && data?.destinationCityId ? data?.destinationCityId : null,
      factor: null,
      createdBy: 0,
      createdOn: null,
      isFinalSubmit: false,
      finalSubmitOn: null,
      inputValue: 0,
      supervisorUserId:
        data && data?.supervisorUserId ? data?.supervisorUserId : null,
      supervisorApprovalDate:
        data && data?.supervisorApprovalDate
          ? data?.supervisorApprovalDate
          : null,
      supervisorComment:
        data && data?.supervisorComment ? data?.supervisorComment : null,
      managerId: data && data?.managerId ? data?.managerId : null,
      managerApprovalDate:
        data && data?.managerApprovalDate ? data?.managerApprovalDate : null,
      managerComment:
        data && data?.managerComment ? data?.managerComment : null,
      entryDate: [
        this._datepipe.transform(new Date(), 'dd MMM yyyy'),

        Validators.required,
      ],
      fromdate: [null, Validators.required],
      todate: [null, Validators.required],
      indexId: [null],
      industrialValue: [null, Validators.required],
      commercialValue: [null, Validators.required],
      householdValue: [null, Validators.required],
      transportValue: [null, Validators.required],
      unitId: [
        this.unitData.length == 1 ? this.unitData[0].id : null,
        Validators.required,
      ],
      isDataChanged: false,
    });
  }

  fillDataInForm(accordionData: any) {
    if (accordionData && accordionData?.length > 0) {
      this.avoidanceForm = this.fb.group({
        tableData: this.fb.array([]),
      });
      accordionData.forEach((data: any, index: any) => {
        this.addTableRow(data, index); // Add each object from contactData to the form
      });
    } else {
      this.avoidanceForm = this.fb.group({
        tableData: this.fb.array([this.createTableRow()]),
      });
      console.log(this.avoidanceForm.value);
    }

    if (this.apiData?.childStartMonth || this.apiData?.childEndMonth) {
      this.minDate = moment(this.apiData?.childStartMonth);
      this.maxDate = moment(this.apiData?.childEndMonth);
    }
  }

  addTableRow(data: any, index: any) {
    const newRow: any = this.createTableRow(); // Create a new form group
    if (data.id != 0) data.indexId = 0;
    newRow.patchValue(data); // Set the values from the data

    !this.apiData?.actionDetail?.includes(this.actionType.editAction)
      ? newRow?.disable()
      : '';
    this.completeFuelForms.push(newRow);
  }

  checkError() {
    const formArray = this.avoidanceForm.get('tableData') as FormArray;
    if (formArray.invalid) {
      this.isCheckingError = true;
      formArray.markAllAsTouched();
      return;
    } else {
      this.isCheckingError = false;
    }

    console.log(formArray, 'input cop');
  }

  getEmissionValue(index: any) {
    const row: any = this.getFormRow(index);
    if (
      row.controls.commercialValue.value ||
      row.controls.industrialValue.value ||
      row.controls.transportValue.value ||
      row.controls.householdValue.value
    ) {
      this.spinner.show();
      let data = row;

      this.updateDataToParent.emit({
        isIdAvailable: data?.value?.id ? true : false,
        isIndexAvailable: data?.value?.indexId ? true : false,
        data: data,
        childIndex: index,
      });
      this.spinner.hide();
    }
  }

  onClearColumn(index: number) {
    const row: any = this.getFormRow(index);
    if (this.apiData?.actionDetail?.includes(this.actionType.editAction)) {
      row?.controls?.fromdate?.reset();
      row?.controls?.todate?.reset();
      row?.controls?.householdValue?.reset();
      row?.controls?.commercialValue?.reset();
      row?.controls?.transportValue?.reset();
      row?.controls?.industrialValue?.reset();
    }
  }

  removeName(index: number) {
    const tableDataArray = this.avoidanceForm?.get('tableData') as FormArray;
    const row: any = tableDataArray.at(index);
    if (this.apiData?.actionDetail?.includes(this.actionType.deleteAction)) {
      if (tableDataArray?.getRawValue()?.length > 1) {
        if (row?.value?.id && row?.value?.id != 0) {
          this.spinner.show();
          let requestBody = {
            requestId: 'string',
            data: {
              ghgItemID: row.value.id,
            },
            message: 'string',
          };

          this.ghgAvoidanceService
            .deleteAccordionData(requestBody)
            .subscribe((res: any) => {
              this.completeFuelForms.removeAt(index);
              this.deleteAccordionData.emit({
                id: row.value.id,
                isIndexId: false,
              });
              this.isCheckingError = false;
              this.isDateOverlappingChecking[index] = false;
              this.messageService.successToast('Data deleted successfully.');

              this.spinner.hide();
            });
        } else {
          this.completeFuelForms.removeAt(index);
          this.isDateOverlappingChecking[index] = false;
          this.isCheckingError = false;
          if (row.value.indexId)
            this.deleteAccordionData.emit({
              id: row.value.indexId,
              isIndexId: true,
            });
          this.messageService.successToast('Data deleted successfully.');
        }
      } else {
        this.messageService.errorToast('Please enter one more entry first');
      }
    }
  }

  getFuelFormData() {
    let formData: any = this.avoidanceForm.get('tableData'); // Emit the updated contactData
    return formData;
  }

  onStartDateSelected(index: any) {
    const row: any = this.getFormRow(index);
    this.disableToDate[index] = new Date(row.controls.fromdate.value);
  }

  addNewObject(index: any) {
    console.log('add new object call');

    const tableDataArray = this.avoidanceForm?.get('tableData') as FormArray;
    const row: any = tableDataArray.at(index);
    if (
      row?.value?.fromdate &&
      row?.value?.todate &&
      tableDataArray?.controls?.length > 1
    ) {
      if (
        !this.isDateRangeOverlapping(
          row?.value?.fromdate,
          row?.value?.todate,
          tableDataArray,
          index
        )
      ) {
        this.isDateOverlappingChecking[index] = false;
      } else {
        this.messageService.errorToast(
          'Date range should not overlap with existing date ranges'
        );
        this.isDateOverlappingChecking[index] = true;
        setTimeout(() => {
          row.get('todate')?.setErrors({ customError: true });
          row.get('fromdate')?.setErrors({ customError: true });
          console.log('settytimeout');
        }, 0);
      }
    }
  }

  isDateRangeOverlapping(
    startDate: any,
    endDate: any,
    arrayOfObjects: any,
    index: any
  ) {
    console.log('is daterange xal');

    let isTrue: boolean = false;
    arrayOfObjects?.controls?.forEach((obj: any, index1: any) => {
      const row: any = arrayOfObjects.at(index1);
      if (
        ((startDate >= moment(obj?.value.fromdate) &&
          startDate <= moment(obj?.value.todate)) ||
          (endDate >= moment(obj?.value.fromdate) &&
            endDate <= moment(obj?.value.todate)) ||
          (startDate <= moment(obj?.value.fromdate) &&
            endDate >= moment(obj?.value.todate))) &&
        index != index1
      ) {
        isTrue = true; // Overlapping date range found
      } else {
        row.get('todate')?.setErrors(null);
        row.get('fromdate')?.setErrors(null);
        row.get('todate')?.markAllAsTouched();
        row.get('fromdate')?.markAllAsTouched();
      }
    });
    return isTrue; // No overlapping date range found
  }

  tellingParentToFieldIsActive(index: any) {
    const row: any = this.getFormRow(index);
    if (row?.controls?.id?.value) this.isFieldIsTouched.emit(true);
  }

  onInputChange(event: any, index: any, controlName: any) {
    const input = event.target.value;
    // Remove non-numeric characters\
    event.target.value = input.replace(
      /[^0-9.]|(\.(.*\.)+.*$)|(?<=\.\d{2})./g,
      ''
    );
    const row: any = this.getFormRow(index);
    row.controls[controlName].setValue(event.target.value);

    if (!input.includes('.')) this.typingTimer.next(index);
    else {
      if (input.split('.')[1].length > 2) {
        this.messageService.errorToast(
          'Only 2 digits are allowed after the decimal'
        );
      }

      if (input.split('.')[0] == '' && input.split('.')[1] == '') {
        event.target.value = 0 + '.' + 0;
        row.controls[controlName].setValue(event.target.value);
        this.typingTimer.next(index);
      }
    }
  }

  isDataChange(index: any) {
    const row: any = this.getFormRow(index);
    row?.controls?.isDataChanged.setValue(true);
  }
}
