import {
  ChangeDetectorRef,
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  AfterContentChecked,
  ViewChild,
  ElementRef,
} from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
import {
  DateAdapter,
  MAT_DATE_FORMATS,
  MAT_DATE_LOCALE,
} from '@angular/material/core';
import {
  MomentDateAdapter,
  MAT_MOMENT_DATE_ADAPTER_OPTIONS,
} from '@angular/material-moment-adapter';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { GhgEmissionService } from 'src/app/services/manager/ghg-emission.service';
import { MessageService } from 'src/app/services/message.service';
import { ConfirmationDialogComponent } from './../../../shared/confirmation-dialog/confirmation-dialog.component';

export const MY_FORMATS = {
  parse: {
    dateInput: '',
  },
  display: {
    dateInput: 'DD MMM YYYY',
    monthYearLabel: 'MMM YYYY',
  },
};
import { Subject } from 'rxjs';
import { debounceTime } from 'rxjs/operators';
import * as moment from 'moment';
import { Router } from '@angular/router';
import {
  ghgEmissionAvoidanceActionType,
  GhgEmissionScopeHardCodedValue,
} from 'src/app/components/shared/commonData';

@Component({
  selector: 'app-scope3-child',
  templateUrl: './scope3-child.component.html',
  styleUrls: ['./scope3-child.component.scss'],
  providers: [
    {
      provide: DateAdapter,
      useClass: MomentDateAdapter,
      deps: [MAT_DATE_LOCALE, MAT_MOMENT_DATE_ADAPTER_OPTIONS],
    },
    { provide: MAT_DATE_FORMATS, useValue: MY_FORMATS },
  ],
})
export class Scope3ChildComponent {
  @Input() accordionData!: any;
  @Input() apiData!: any;
  @Output() deleteAccordionData = new EventEmitter<any>();
  @Output() updateDataToParent = new EventEmitter<any>();
  @Output() isFieldIsTouched = new EventEmitter<boolean>();
  @Output() isWorkFromOfficeEnable = new EventEmitter<boolean>();
  @Output() triggerParentToUpdateAPI = new EventEmitter<any>();
  @Output() isDateOverLap = new EventEmitter<any>();
  @Output() isRowExcelUploaded = new EventEmitter<any>();
  @ViewChild('scrollBarTable') scrollBarTable!: ElementRef;

  dialogRef!: MatDialogRef<ConfirmationDialogComponent, any>;

  private typingTimer: Subject<any> = new Subject();
  private EmissionTimer: Subject<any> = new Subject();

  scopeForm!: FormGroup;
  entryDate!: Date;
  fuelData: { [key: number]: any } = {};
  isCheckingError!: boolean;
  isDateOverlappingChecking: { [key: number]: boolean } = {};
  disableToDate: { [key: number]: Date } = {};
  factorValue!: any;
  unitListBasedOnIndex: { [key: number]: any } = {};

  minDate = moment(new Date());
  maxDate = moment(new Date());
  subDropdownListBasedOnIndex: { [key: number]: any } = {};
  subDropdownFilterListBasedOnIndex: { [key: number]: any } = {};
  supplierList: { [key: number]: any } = {};
  dropdownItemList: { [key: number]: any } = {};
  employeeList: { [key: number]: any } = {};
  ghgEmissionList: { [key: number]: any } = {};
  ghgEmissionSoldProductList: { [key: number]: any } = {};
  ghgEmissionFranchiseList: { [key: number]: any } = {};
  ghgEmissionInvestmentList: { [key: number]: any } = {};
  addProductInputData: any;
  ghgEmissionLeasedAssetList: { [key: number]: any } = {};
  ghgEmissionWorkArrangement: { [key: number]: any } = {};
  isWorkFromHomeEnable!: boolean;
  startDate: Date = new Date();

  constructor(
    private ghgService: GhgEmissionService,
    private fb: FormBuilder,
    private spinner: NgxSpinnerService,
    private messageService: MessageService,
    private changeDetect: ChangeDetectorRef,
    private router: Router,
    public dialog: MatDialog,
    public commonCatData: GhgEmissionScopeHardCodedValue,
    public actionType: ghgEmissionAvoidanceActionType
  ) {}

  ngOnInit(): void {
    console.log('scope 3 child working.......');
    this.fillDataInForm(this.accordionData);

    this.typingTimer
      .pipe(debounceTime(500)) // Adjust the debounce time as needed (in milliseconds)
      .subscribe((res: any) => {
        this.getEmissionValue(res);
      });

    this.EmissionTimer.pipe(debounceTime(500)) // Adjust the debounce time as needed (in milliseconds)
      .subscribe((res: any) => {
        this.updateDataToParentComponent(res);
      });
  }

  ngAfterContentChecked(): void {
    this.changeDetect.detectChanges();
  }

  cloningDataForDropdownSearch(index: any) {
    this.fuelData[index] = this.accordionData;
    this.supplierList[index] = this.apiData.supplierList;
    this.dropdownItemList[index] = this.apiData.dropdownItemList;
    this.employeeList[index] = this.apiData.employeeList;
    this.ghgEmissionList[index] = this.apiData.ghgEmissionList;
    this.ghgEmissionFranchiseList[index] =
      this.apiData.ghgEmissionFranchiseList;
    this.ghgEmissionInvestmentList[index] =
      this.apiData.ghgEmissionInvestmentList;
    this.ghgEmissionSoldProductList[index] =
      this.apiData.ghgEmissionSoldProductList;
    this.ghgEmissionLeasedAssetList[index] =
      this.apiData.ghgEmissionLeasedAssetList;
    this.ghgEmissionWorkArrangement[index] =
      this.apiData.ghgEmissionWorkArrangement;
  }

  createTableRow(): FormGroup {
    return this.fb.group({
      id: null,
      corporateOfficeId: this.apiData?.corporateOfficeId
        ? this.apiData?.corporateOfficeId
        : null,
      regionalOfficeId: this.apiData?.regionalOfficeId
        ? this.apiData?.regionalOfficeId
        : null,
      siteOfficeId: this.apiData?.siteOfficeId
        ? this.apiData?.siteOfficeId
        : null,
      moduleId: this.apiData?.moduleId ? this.apiData?.moduleId : null,
      subModuleId: this.apiData?.subModuleId ? this.apiData?.subModuleId : null,
      categoryId: this.apiData?.categoryId ? this.apiData?.categoryId : null,
      dropdownTypeId: this.apiData?.dropdownTypeId
        ? this.apiData?.dropdownTypeId
        : null,
      dataCapturingSlotGroupId: this.apiData?.dataCapturingSlotGroupId
        ? Number(this.apiData?.dataCapturingSlotGroupId)
        : null,
      sourceCityId: null,
      destinationCityId: null,
      inputValue: [
        null,
        this.isValidate([
          this.commonCatData.scope3Cat1,
          this.commonCatData.scope3Cat2,
          this.commonCatData.scope3Cat3,
          this.commonCatData.scope3Cat4,
          this.commonCatData.scope3Cat5,
          this.commonCatData.scope3Cat6,
          this.commonCatData.scope3Cat7,
          this.commonCatData.scope3Cat8,
          this.commonCatData.scope3Cat9,
          this.commonCatData.scope3Cat10,
          this.commonCatData.scope3Cat11,
          this.commonCatData.scope3Cat12,
          this.commonCatData.scope3Cat13,
          this.commonCatData.scope3Cat14,
        ])
          ? Validators.required
          : '',
      ],
      unitId: [
        null,
        this.isValidate([
          this.commonCatData.scope3Cat1,
          this.commonCatData.scope3Cat2,
          this.commonCatData.scope3Cat3,
          this.commonCatData.scope3Cat4,
          this.commonCatData.scope3Cat5,
          this.commonCatData.scope3Cat6,
          this.commonCatData.scope3Cat7,
          this.commonCatData.scope3Cat8,
          this.commonCatData.scope3Cat9,
          this.commonCatData.scope3Cat10,
          this.commonCatData.scope3Cat11,
          this.commonCatData.scope3Cat12,
          this.commonCatData.scope3Cat13,
          this.commonCatData.scope3Cat14,
        ])
          ? Validators.required
          : '',
      ],
      factor: [
        null,
        this.isValidate([
          this.commonCatData.scope3Cat1,
          this.commonCatData.scope3Cat2,
          this.commonCatData.scope3Cat3,
          this.commonCatData.scope3Cat4,
          this.commonCatData.scope3Cat5,
          this.commonCatData.scope3Cat6,
          this.commonCatData.scope3Cat8,
          this.commonCatData.scope3Cat9,
          this.commonCatData.scope3Cat10,
          this.commonCatData.scope3Cat11,
          this.commonCatData.scope3Cat12,
          this.commonCatData.scope3Cat13,
          this.commonCatData.scope3Cat14,
        ])
          ? Validators.required
          : '',
      ],
      fromdate: [null, [Validators.required]],
      todate: [null, [Validators.required]],
      dropdownItemId: [null, [Validators.required]],
      supervisorUserId: null,
      supervisorApprovalDate: null,
      supervisorComment: null,
      managerId: null,
      managerApprovalDate: null,
      managerComment: null,
      entryDate: [new Date(), [Validators.required]],
      emission: [null, [Validators.required]],
      indexId: null,
      ghgemissionSourceTypeId: null,
      ghgemissionInvestmentPortfolioId: [
        null,
        this.isValidate([this.commonCatData.scope3Cat15])
          ? Validators.required
          : '',
      ],
      ghgemissionFranchiseId: [
        null,
        this.isValidate([this.commonCatData.scope3Cat14])
          ? Validators.required
          : '',
      ],
      ghgemissionLeasedAssetId: [
        null,
        this.isValidate([
          this.commonCatData.scope3Cat8,
          this.commonCatData.scope3Cat13,
        ])
          ? Validators.required
          : '',
      ],
      ghgemissionSoldProductId: [
        null,
        this.isValidate([
          this.commonCatData.scope3Cat10,
          this.commonCatData.scope3Cat11,
          this.commonCatData.scope3Cat12,
        ])
          ? Validators.required
          : '',
      ],
      ghgemissionEmplyoeeId: [
        null,
        this.isValidate([this.commonCatData.scope3Cat7])
          ? Validators.required
          : '',
      ],
      ghgemissionWorkArrangementId: [
        null,
        this.isValidate([this.commonCatData.scope3Cat7])
          ? Validators.required
          : '',
      ],
      supplierId: [
        null,
        this.isValidate([
          this.commonCatData.scope3Cat1,
          this.commonCatData.scope3Cat2,
          this.commonCatData.scope3Cat3,
        ])
          ? Validators.required
          : '',
      ],
      ghgitemStatusId: null,
      subdropdownItemId: [
        null,
        this.isValidate([
          this.commonCatData.scope3Cat4,
          this.commonCatData.scope3Cat6,
          this.commonCatData.scope3Cat7,
          this.commonCatData.scope3Cat9,
          this.commonCatData.scope3Cat15,
        ])
          ? Validators.required
          : '',
      ],
      isFinalSubmit: false,
      isDataChanged: false,
    });
  }

  isValidate(arr: any) {
    return arr.includes(this.apiData.categoryId) ? true : false;
  }

  removeValidations(index: any) {
    const row: any = this.getFormRow(index);

    if (Number(row.value.dropdownItemId) == 2660) {
      row.controls.factor.setValidators(null);
      row.controls.emission.setValidators(null);
      row.controls.inputValue.setValidators(null);
      row.controls.emission.updateValueAndValidity();
      row.controls.factor.updateValueAndValidity();
      row.controls.inputValue.updateValueAndValidity();
      row.controls.factor.setValue('0');
      // row.controls.f.updateValueAndValidity();
    }
  }

  fillDataInForm(accordionData: any) {
    if (accordionData && accordionData?.length > 0) {
      this.scopeForm = this.fb.group({
        tableData: this.fb.array([]),
      });
      accordionData.forEach((data: any, index: any) => {
        this.addTableRow(data, index); // Add each object from contactData to the form
      });
    } else {
      this.scopeForm = this.fb.group({
        tableData: this.fb.array([this.createTableRow()]),
      });
      this.cloningDataForDropdownSearch(0);
    }

    if (this.apiData?.childStartMonth || this.apiData?.childEndMonth) {
      this.minDate = moment(this.apiData?.childStartMonth);
      this.maxDate = moment(this.apiData?.childEndMonth);
      this.startDate = new Date(this.apiData?.childStartMonth);
    }
  }

  get completeFuelForms() {
    return this.scopeForm?.get('tableData') as FormArray;
  }

  getFormRow(index: any) {
    return this.completeFuelForms.at(index);
  }

  addTableRow(data: any, index: any) {
    const newRow: any = this.createTableRow(); // Create a new form group
    if (data.id != null) data.indexId = 0;
    newRow.patchValue(data); // Set the values from the data object
    !this.apiData?.actionDetail?.includes(this.actionType?.editAction)
      ? newRow?.disable()
      : '';
    newRow.controls.unitId.enable();
    this.completeFuelForms.push(newRow);

    this.cloningDataForDropdownSearch(index);
    if (!this.apiData?.actionDetail?.includes(this.actionType?.editAction)) {
      this.unitListBasedOnIndex[index] = [];
      this.apiData?.masterCompleteUnitList?.forEach((ele: any) => {
        if (ele?.id == data?.unitId)
          this.unitListBasedOnIndex[index] = [
            { unitId: ele?.id, unit: ele?.unit },
          ];
      });
    } else this.getUnitData(index, 'child');

    this.getSubDropdownItemList(index, '');
    if (data.categoryId == this.commonCatData.scope3Cat7)
      this.workArrangementChange(
        { value: data.ghgemissionWorkArrangementId },
        index,
        'ts'
      );
  }

  addTableColumn() {
    if (this.apiData?.actionDetail?.includes(this.actionType.addAction)) {
      this.spinner.show();
      this.completeFuelForms.push(this.createTableRow());
      setTimeout(() => {
        if (this.completeFuelForms?.controls?.length > 5)
          this.scrollBarTable.nativeElement.scrollTop =
            this.scrollBarTable.nativeElement.scrollHeight;
        else window.scrollTo(0, document.body.scrollHeight + 100);
        this.spinner.hide();
      }, 0);
    }
  }

  onClearColumn(index: number) {
    const row: any = this.getFormRow(index);
    if (this.apiData?.actionDetail?.includes(this.actionType?.editAction)) {
      row?.controls?.dropdownItemId?.reset();
      row?.controls?.subdropdownItemId?.reset();
      row?.controls?.fromdate?.reset();
      row?.controls?.todate?.reset();
      row?.controls?.inputValue?.reset();
      row?.controls?.unitId?.reset();
      row?.controls?.factor?.reset();
      row?.controls?.emission?.reset();
      row?.controls?.supplierId?.reset();
      row?.controls?.ghgemissionEmplyoeeId?.reset();
      row?.controls?.ghgemissionSoldProductId?.reset();
      row?.controls?.ghgemissionFranchiseId?.reset();
      row?.controls?.ghgemissionInvestmentPortfolioId?.reset();
      row?.controls?.ghgemissionSourceTypeId?.reset();
    }
    if (this.isDateOverlappingChecking[index]) {
      this.isDateOverLap.emit(false);
    }
  }

  removeName(index: number) {
    const tableDataArray = this.scopeForm?.get('tableData') as FormArray;
    const row: any = tableDataArray.at(index);
    if (tableDataArray?.getRawValue()?.length > 1) {
      if (row?.value?.id && row?.value?.id != 0) {
        this.spinner.show();
        let requestBody = {
          requestId: 'string',
          data: {
            ghgItemID: row.value.id,
          },
          message: 'string',
        };
        this.ghgService
          .deleteScope3AccordionData(requestBody)
          .subscribe((res: any) => {
            this.completeFuelForms.removeAt(index);
            this.deleteAccordionData.emit({
              id: row.value.id,
              isIndexId: false,
            });
            this.isCheckingError = false;
            if (this.isDateOverlappingChecking[index]) {
              this.isDateOverLap.emit(false);
              this.isDateOverlappingChecking[index] = false;
            }
            this.messageService.successToast('Data deleted successfully.');

            this.spinner.hide();
          });
      } else {
        this.isCheckingError = false;
        this.completeFuelForms.removeAt(index);
        if (row.value.indexId)
          this.deleteAccordionData.emit({
            id: row.value.indexId,
            isIndexId: true,
          });
        if (this.isDateOverlappingChecking[index]) {
          this.isDateOverLap.emit(false);
          this.isDateOverlappingChecking[index] = false;

          // markAllAsTouched()
        }
        this.messageService.successToast('Data deleted successfully.');
      }
    } else {
      this.messageService.errorToast('Please enter one more entry first');
    }
  }

  onInputChange(event: any, index: any) {
    const input = event.target.value;
    // Remove non-numeric characters\
    event.target.value = input.replace(
      /[^0-9.]|(\.(.*\.)+.*$)|(?<=\.\d{2})./g,
      ''
    );
    const row: any = this.getFormRow(index);
    row.controls.inputValue.setValue(event.target.value);
    this.typingTimer.next(index);
  }

  onEmissionChange(event: any, index: any) {
    const input = event.target.value;
    // Remove non-numeric characters\
    event.target.value = input.replace(/[^0-9.]|(\.(.*\.)+.*$)/g, '');
    this.EmissionTimer.next(index);
  }

  getFuelFormData() {
    let formData: any = this.scopeForm.get('tableData'); // Emit the updated contactData
    return formData;
  }

  updateDateValue(fieldName: any, event: any, index: any) {
    if (event?.target?.value) {
      const row: any = this.getFormRow(index);
      row[fieldName] = event?.target?.value;
    }
  }

  checkError() {
    this.isCheckingError = true;
    const formArray = this.scopeForm.get('tableData') as FormArray;
    if (formArray.invalid) {
      formArray.markAllAsTouched();
    }
  }

  onStartDateSelected(index: any) {
    const row: any = this.getFormRow(index);
    this.disableToDate[index] = new Date(row.controls.fromdate.value);
  }

  getEmissionValue(index: any) {
    const row: any = this.getFormRow(index);
    let isSubDropdownEnable =
      [
        this.commonCatData.scope3Cat1,
        this.commonCatData.scope3Cat2,
        this.commonCatData.scope3Cat3,
        this.commonCatData.scope3Cat5,
        this.commonCatData.scope3Cat8,
        this.commonCatData.scope3Cat10,
        this.commonCatData.scope3Cat11,
        this.commonCatData.scope3Cat12,
        this.commonCatData.scope3Cat13,
        this.commonCatData.scope3Cat14,
      ].includes(this.apiData.categoryId) ||
      (this.apiData.categoryId == this.commonCatData.scope3Cat7 &&
        row.value.dropdownItemId == '2660')
        ? false
        : true;

    if (
      row.controls.dropdownItemId.value &&
      row.controls.unitId.value &&
      (isSubDropdownEnable
        ? this.subDropdownListBasedOnIndex[index]?.length == 0
          ? true
          : row.controls.subdropdownItemId.value
        : true) &&
      row.controls.fromdate.value &&
      row.controls.todate.value
    )
      this.getFactorValue(row, isSubDropdownEnable);

    if (
      row.controls.dropdownItemId.value &&
      row.controls.unitId.value &&
      (this.apiData.categoryId == this.commonCatData.scope3Cat15
        ? true
        : row.controls.inputValue.value) &&
      (isSubDropdownEnable
        ? this.subDropdownListBasedOnIndex[index]?.length == 0
          ? true
          : row.controls.subdropdownItemId.value
        : true) &&
      row.controls.fromdate.value &&
      row.controls.todate.value
    ) {
      this.spinner.show();
      let requestBody: any = {
        requestUserLoginName: JSON.parse(localStorage.getItem('userData')!)
          .username,
        requestId: 'string',
        data: {
          startDate: row.controls.fromdate.value,
          endDate: row.controls.todate.value,
          dropdownItemId: Number(row.controls.dropdownItemId.value),
          unitId: Number(row.controls.unitId.value),
          value: Number(row.controls.inputValue.value),
        },
        message: 'string',
      };
      if (isSubDropdownEnable)
        requestBody.data['subDropdownItemId'] =
          this.subDropdownListBasedOnIndex[index]?.length == 0
            ? row.controls.subdropdownItemId.value
            : Number(row.controls.subdropdownItemId.value);
      this.ghgService.getEmissionForFuel(requestBody).subscribe((res: any) => {
        if (res?.isSuccess) {
          row.controls.emission.setValue(res?.data);
          this.updateDataToParentComponent(index);
        } else {
          this.messageService.errorToast(res?.errorMessage);
          row.controls.emission.setValue(null);
        }
        this.spinner.hide();
      });
    }
  }

  getFactorValue(row: any, isSubDropdownEnable: boolean) {
    this.spinner.show();
    let requestBody: any = {
      // startDate:
      //   new Date().getFullYear() +
      //   '-' +
      //   new Date().getMonth() +
      //   '-' +
      //   new Date().getDate(),
      fromDate: `${Number(new Date(row.controls.fromdate.value).getMonth() + 1)}/${new Date(row.controls.fromdate.value).getDate()}/${new Date(row.controls.fromdate.value).getFullYear()}`,
      toDate: `${Number(new Date(row.controls.todate.value).getMonth() + 1)}/${new Date(row.controls.todate.value).getDate()}/${new Date(row.controls.todate.value).getFullYear()}`,
      // toDate: new Date(row.controls.todate.value).toLocaleDateString(),
      dropdownItemId: row.controls.dropdownItemId.value,
      unitId: row.controls.unitId.value,
    };
    if (isSubDropdownEnable)
      requestBody['SubDropdownItemId'] = row.controls.subdropdownItemId.value;
    this.ghgService.getFactorForFuel(requestBody).subscribe((res: any) => {
      this.factorValue = res.data;
      if (res?.isSuccess)
        row.controls.factor.setValue(`${res?.data?.factorValue}`);
      else {
        this.messageService.errorToast(res?.errorMessage);
        row.controls.factor.setValue(null);
      }
      this.spinner.hide();
    });
  }

  getUnitData(index: any, apiCallingType: string) {
    this.unitListBasedOnIndex[index] = [];
    const row: any = this.getFormRow(index);

    let requestBody = {};
    let isConditionMatch = false;

    if (
      [
        this.commonCatData.scope3Cat1,
        this.commonCatData.scope3Cat2,
        this.commonCatData.scope3Cat3,
        this.commonCatData.scope3Cat5,
        this.commonCatData.scope3Cat8,
        this.commonCatData.scope3Cat10,
        this.commonCatData.scope3Cat11,
        this.commonCatData.scope3Cat12,
        this.commonCatData.scope3Cat13,
        this.commonCatData.scope3Cat14,
      ].includes(this.apiData.categoryId)
    ) {
      requestBody = {
        dropdownItemId: Number(row.controls.dropdownItemId.value),
      };
      if (
        row.controls.dropdownItemId.value &&
        row.controls.dropdownItemId.value != 'null'
      )
        isConditionMatch = true;
    } else {
      requestBody = {
        dropdownItemId: Number(row.controls.dropdownItemId.value),
        subdropdownItemId: [
          this.commonCatData.scope3Cat6,
          this.commonCatData.scope3Cat7,
          this.commonCatData.scope3Cat9,
        ].includes(this.apiData.categoryId)
          ? Number(row.controls.subdropdownItemId.value) ?? null
          : Number(row.controls.subdropdownItemId.value),
      };
      if (
        row.controls.dropdownItemId.value &&
        row.controls.dropdownItemId.value != 'null' &&
        [
          this.commonCatData.scope3Cat6,
          this.commonCatData.scope3Cat7,
          this.commonCatData.scope3Cat9,
        ].includes(this.apiData.categoryId)
          ? true
          : row.controls.subdropdownItemId.value &&
            row.controls.subdropdownItemId.value != 'null'
      )
        isConditionMatch = true;
    }
    if (isConditionMatch) {
      this.spinner.show();
      this.ghgService
        .getUnitListBasedOnFuelType(requestBody)
        .subscribe((res: any) => {
          if (
            !this.apiData?.actionDetail?.includes(this.actionType?.editAction)
          ) {
            res.data.forEach((ele: any) => {
              if (ele.unitId == row.controls.unitId.value)
                this.unitListBasedOnIndex[index] = [ele];
            });
          } else this.unitListBasedOnIndex[index] = res.data;

          if (res?.data && res?.data?.length == 1 && apiCallingType == 'ui') {
            row.controls.unitId.setValue(res.data[0].unitId);
            this.getEmissionValue(index);
          }
          this.spinner.hide();
        });
    }
  }

  addNewObject(index: any) {
    const tableDataArray = this.scopeForm?.get('tableData') as FormArray;
    const row: any = tableDataArray.at(index);
    if (
      row?.value?.fromdate &&
      row?.value?.todate &&
      tableDataArray?.controls?.length > 1
    ) {
      if (!this.isDateRangeOverlapping(row?.value, tableDataArray, index)) {
        this.isDateOverlappingChecking[index] = false;
        this.isDateOverLap.emit(false);
      } else {
        this.messageService.errorToast(
          'Date range should not overlap with existing date ranges'
        );
        this.isDateOverlappingChecking[index] = true;
        setTimeout(() => {
          row.get('todate')?.setErrors({ customError: true });
          row.get('fromdate')?.setErrors({ customError: true });
        }, 0);
        this.isDateOverLap.emit(true);
      }
    }
  }

  isDateRangeOverlapping(requestObj: any, arrayOfObjects: any, index: any) {
    let isTrue: boolean = false;

    arrayOfObjects?.controls?.forEach((obj1: any, index1: any) => {
      // obj - iteration loop , requestObj - current obj
      let obj = obj1?.controls;
      const row: any = arrayOfObjects.at(index1);

      if (index != index1) {
        const dateOverlap =
          (requestObj?.fromdate >= moment(obj?.fromdate?.value) &&
            requestObj?.fromdate <= moment(obj?.todate?.value)) ||
          (requestObj?.todate >= moment(obj?.fromdate?.value) &&
            requestObj?.todate <= moment(obj?.todate?.value)) ||
          (requestObj?.fromdate <= moment(obj?.fromdate?.value) &&
            requestObj?.todate >= moment(obj?.todate?.value));

        // let cat7 =

        if (
          dateOverlap &&
          requestObj.ghgemissionInvestmentPortfolioId ==
            obj.ghgemissionInvestmentPortfolioId?.value &&
          requestObj.ghgemissionFranchiseId ==
            obj.ghgemissionFranchiseId?.value &&
          requestObj.ghgemissionSoldProductId ==
            obj.ghgemissionSoldProductId?.value &&
          requestObj.ghgemissionSourceTypeId ==
            obj.ghgemissionSourceTypeId?.value &&
          requestObj.ghgemissionEmplyoeeId ==
            obj.ghgemissionEmplyoeeId?.value &&
          requestObj.ghgemissionLeasedAssetId ==
            obj.ghgemissionLeasedAssetId?.value &&
          requestObj.ghgemissionSoldProductId ==
            obj.ghgemissionSoldProductId?.value &&
          requestObj.ghgemissionEmplyoeeId ==
            obj.ghgemissionEmplyoeeId?.value &&
          requestObj.supplierId == obj.supplierId?.value
        ) {
          if (requestObj.categoryId == this.commonCatData?.scope3Cat7) {
            isTrue = true;
            return;
          } else if (
            requestObj.dropdownItemId == obj.dropdownItemId?.value &&
            requestObj.subdropdownItemId == obj.subdropdownItemId?.value &&
            requestObj.ghgemissionWorkArrangementId ==
              obj.ghgemissionWorkArrangementId?.value
          ) {
            isTrue = true;
            return;
          } else {
            row.get('todate')?.setErrors(null);
            row.get('fromdate')?.setErrors(null);
            row.get('todate')?.markAllAsTouched();
            row.get('fromdate')?.markAllAsTouched();
            return; // Early exit if overlap found
          }
        }
      } else {
        row.get('todate')?.setErrors(null);
        row.get('fromdate')?.setErrors(null);
        row.get('todate')?.markAllAsTouched();
        row.get('fromdate')?.markAllAsTouched();
      }
    });
    return isTrue; // No overlapping date range found
  }

  getSubDropdownItemList(index: any, type: string) {
    this.spinner.show();
    this.subDropdownListBasedOnIndex[index] = [];
    this.subDropdownFilterListBasedOnIndex[index] = [];
    const row: any = this.getFormRow(index);
    if (type == 'ui') row.controls.subdropdownItemId.setValue(null);
    let requestBody = {
      requestUserLoginName: JSON.parse(localStorage.getItem('userData')!)
        .username,
      requestId: 'string',
      data: {
        dropDownItemId: Number(row.controls.dropdownItemId.value),
      },
      message: 'string',
    };
    this.ghgService
      .getSubDropdownItemList(requestBody)
      .subscribe((res: any) => {
        this.subDropdownListBasedOnIndex[index] = res.data;
        this.subDropdownFilterListBasedOnIndex[index] = res.data;
        row.controls.subdropdownItemId.setValidators(null);
        if (
          type &&
          this.apiData?.actionDetail?.includes(this.actionType?.editAction)
        ) {
          this.getEmissionValue(index);
          this.getUnitData(index, 'ui');
        }
        this.spinner.hide();
      });
  }

  addSupplier(event: any, index: any) {
    const row: any = this.getFormRow(index);
    if (event.value == 'addSupplier') {
      this.confirmationDialogToAdd(index);
    }
  }

  getFactorValueToShow(index: number): any {
    const control = this.completeFuelForms.get(index.toString()); // Convert index to string
    return control?.value?.factor
      ? Number(control?.value?.factor)?.toFixed(5)
      : 0;
  }

  confirmationDialog(index: any) {
    const row: any = this.getFormRow(index);
    if (this.apiData?.actionDetail?.includes(this.actionType?.deleteAction)) {
      this.dialogRef = this.dialog.open(ConfirmationDialogComponent, {
        width: '24.514vw',
        height: '12.083vw',
        data: {
          header: 'Delete?',
          data: {
            bodyData: `Are you sure, you want to Delete this data ?`,
          },
          button: {
            isSubmit: false,
            isCancel: true,
            isDelete: true,
          },
        },
      });
      this.dialogRef.disableClose = true;

      this.dialogRef.afterClosed().subscribe(result => {
        if (result == 'ok') {
          this.removeName(index);
        }
      });
    }
  }

  confirmationDialogToAdd(index: any) {
    const row: any = this.getFormRow(index);
    if (this.apiData?.actionDetail?.includes(this.actionType?.editAction)) {
      this.dialogRef = this.dialog.open(ConfirmationDialogComponent, {
        width: '24.514vw',
        height: '12.083vw',
        data: {
          header: 'Adding Data?',
          data: {
            bodyData: `Are you sure, you want to Add Supplier Name ?`,
          },
          button: {
            isSubmit: true,
            isCancel: true,
            isDelete: false,
            submitbtnText: 'Add',
          },
        },
      });
      this.dialogRef.disableClose = true;

      this.dialogRef.afterClosed().subscribe(result => {
        if (result == 'ok') {
          this.router.navigate(['/add_supplier']);
        }
        row.controls.supplierId.setValue(null);
      });
    }
  }

  updateDataToParentComponent(index: any) {
    const row: any = this.getFormRow(index);
    let data = row;

    if (this.apiData?.categoryId == this.commonCatData.scope3Cat15) {
      row?.controls?.inputValue?.setValue(row?.value?.emission);
      row.controls?.factor?.setValue('1');
    }
    this.updateDataToParent.emit({
      isIdAvailable: data?.controls?.id?.value ? true : false,
      isIndexAvailable: data?.controls?.indexId.value ? true : false,
      data: data,
      childIndex: index,
    });
  }

  tellingParentToFieldIsActive(index: any) {
    const row: any = this.getFormRow(index);
    if (row?.controls?.id?.value) this.isFieldIsTouched.emit(true);
  }

  OpenModel(event: any, index: any, model: any, isSubDropdown: boolean) {
    const row: any = this.getFormRow(index);
    if (event?.value == 'addSoldProduct') {
      model.show();
      row.controls.ghgemissionSoldProductId.setValue(null);
    } else if (event?.value == 'addFranchiseName') {
      model.show();
      row.controls.ghgemissionFranchiseId.setValue(null);
    } else if (event?.value == 'addInvestmentName') {
      model.show();
      row.controls.ghgemissionInvestmentPortfolioId.setValue(null);
    } else if (event?.value == 'leasedAsset') {
      model.show();
      row.controls.ghgemissionLeasedAssetId.setValue(null);
    } else {
      this.getEmissionValue(index);
      this.getUnitData(index, 'ui');
      this.addNewObject(index);
      this.tellingParentToFieldIsActive(index);
      if (isSubDropdown) this.getSubDropdownItemList(index, 'ui');
    }
  }

  saveSolidAndInvestmentAndFranchiseData(model: any) {
    this.spinner.show();
    if (this.addProductInputData) {
      let requestBody: any = {
        requestUserLoginName: JSON.parse(localStorage.getItem('userData')!)
          .username,
        requestId: 'string',
        data: [],
        message: 'string',
      };

      if (
        [
          this.commonCatData.scope3Cat10,
          this.commonCatData.scope3Cat11,
          this.commonCatData.scope3Cat12,
        ].includes(this.apiData?.categoryId)
      ) {
        requestBody.data.push({ soldProductName: this.addProductInputData });
        this.ghgService.addSoldProduct(requestBody).subscribe((res: any) => {
          if (!res.isSuccess) {
            this.messageService.errorToast(res?.errorMessage);
          } else {
            this.triggerParentToUpdateAPI.emit('sold');
            this.messageService.successToast('Data added successfully.');
            this.addProductInputData = null;
            model.hide();
          }
          this.spinner.hide();
        });
      } else if (
        [this.commonCatData.scope3Cat14].includes(this.apiData?.categoryId)
      ) {
        requestBody.data.push({ franchiseName: this.addProductInputData });
        this.ghgService.addFranchise(requestBody).subscribe((res: any) => {
          if (!res.isSuccess) {
            this.messageService.errorToast(res?.errorMessage);
          } else {
            this.triggerParentToUpdateAPI.emit('sold');
            this.messageService.successToast('Data added successfully.');
            this.addProductInputData = null;
            model.hide();
          }
          this.spinner.hide();
        });
      } else if (
        [this.commonCatData.scope3Cat15].includes(this.apiData?.categoryId)
      ) {
        requestBody.data.push({
          investmentPortfolio: this.addProductInputData,
        });
        this.ghgService
          .addInvestmentPortfolio(requestBody)
          .subscribe((res: any) => {
            if (!res.isSuccess) {
              this.messageService.errorToast(res?.errorMessage);
            } else {
              this.triggerParentToUpdateAPI.emit('sold');
              this.messageService.successToast('Data added successfully.');
              this.addProductInputData = null;
              model.hide();
            }
            this.spinner.hide();
          });
      } else if (
        [
          this.commonCatData.scope3Cat8,
          this.commonCatData.scope3Cat13,
        ].includes(this.apiData?.categoryId)
      ) {
        requestBody.data.push({ leasedAssetName: this.addProductInputData });
        this.ghgService.addLeasedAsset(requestBody).subscribe((res: any) => {
          if (!res.isSuccess) {
            this.messageService.errorToast(res?.errorMessage);
          } else {
            this.triggerParentToUpdateAPI.emit('sold');
            this.messageService.successToast('Data added successfully.');
            this.addProductInputData = null;
            model.hide();
          }
          this.spinner.hide();
        });
      }
    }
  }

  workArrangementChange(event: any, index: any, type: string) {
    const row: any = this.getFormRow(index);
    this.isWorkFromHomeEnable = false;
    this.ghgEmissionWorkArrangement[index].forEach((ele: any) => {
      if (ele.id == event.value && ele.workArrangementName == 'WFH') {
        this.isWorkFromHomeEnable = true;
        row.controls.dropdownItemId.disable();
        row.controls.subdropdownItemId.disable();
        row.controls.inputValue.disable();
        row.controls.unitId.disable();
      }
    });

    if (this.isWorkFromHomeEnable) {
      this.changeDetect.detectChanges();
      let requestBody = {
        requestUserLoginName: JSON.parse(localStorage.getItem('userData')!)
          .username,
        requestId: 'string',
        data: {
          dropDownItemId: Number(this.apiData?.dropdownItemList[0].id),
        },
        message: 'string',
      };
      this.ghgService
        .getSubDropdownItemList(requestBody)
        .subscribe((res: any) => {
          let requestBody1 = {
            dropdownItemId: Number(this.apiData?.dropdownItemList[0].id),
            subdropdownItemId: Number(res?.data[0]?.id),
          };
          this.ghgService
            .getUnitListBasedOnFuelType(requestBody1)
            .subscribe((res: any) => {
              this.unitListBasedOnIndex[index] = res.data;
              row.controls.unitId.setValue(res?.data[0]?.unitId);
              row.controls.emission.setValue(0);
              row.controls.inputValue.setValue(0);
              row.controls.factor.setValue(0);
              row.controls.dropdownItemId.setValue(null);
              row.controls.subdropdownItemId.setValue(null);

              row.controls.dropdownItemId.setValidators(null);
              row.controls.dropdownItemId.updateValueAndValidity();

              row.controls.subdropdownItemId.setValidators(null);
              row.controls.subdropdownItemId.updateValueAndValidity();

              row.controls.inputValue.setValidators(null);
              row.controls.inputValue.updateValueAndValidity();

              row.controls.emission.setValidators(null);
              row.controls.emission.updateValueAndValidity();

              row.controls.factor.setValidators(null);
              row.controls.factor.updateValueAndValidity();
              this.isWorkFromOfficeEnable.emit(false);
              this.updateDataToParentComponent(index);
              this.spinner.hide();
            });
        });
    } else if (type == 'ui') {
      row.controls.emission.setValue(null);
      row.controls.inputValue.setValue(null);
      row.controls.unitId.setValue(null);

      row.controls.unitId.enable();
      row.controls.dropdownItemId.enable();
      row.controls.inputValue.enable();
      row.controls.subdropdownItemId.enable();

      this.unitListBasedOnIndex[index] = [];

      row.controls.dropdownItemId.setValidators([Validators.required]);
      row.controls.dropdownItemId.updateValueAndValidity();

      row.controls.subdropdownItemId.setValidators([Validators.required]);
      row.controls.subdropdownItemId.updateValueAndValidity();

      row.controls.inputValue.setValidators([Validators.required]);
      row.controls.inputValue.updateValueAndValidity();

      row.controls.emission.setValidators([Validators.required]);
      row.controls.emission.updateValueAndValidity();

      row.controls.factor.setValidators([Validators.required]);
      row.controls.factor.updateValueAndValidity();
      this.isWorkFromOfficeEnable.emit(true);
    }
  }

  onSelectFiles(event: any) {
    const file = event?.target?.files[0];
    if (
      file.type ==
        'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' ||
      file.type == 'application/vnd.ms-excel'
    ) {
      const fileSizeInKB = file?.size / 1024;
      if (fileSizeInKB >= 51) {
        return this.messageService.errorToast(
          'Please upload an image less than 50KB'
        );
      }
      this.uploadRowExcelFile(file, event);
    } else {
      return this.messageService.errorToast('Please upload only excel file');
    }
  }

  uploadRowExcelFile(file: any, event: any) {
    this.spinner.show();
    let formData: any = new FormData();
    formData.append(
      'requestUserLoginName',
      JSON.parse(localStorage.getItem('userData')!).username
    );
    formData.append('requestId', null);
    formData.append(
      'data.CorporateOfficeId',
      this.apiData?.corporateOfficeId
        ? Number(this.apiData.corporateOfficeId)
        : 0
    );
    formData.append(
      'data.RegionalOfficeId',
      this.apiData?.regionalOfficeId ? Number(this.apiData.regionalOfficeId) : 0
    );
    formData.append(
      'data.SiteOfficeId',
      this.apiData?.siteOfficeId ? Number(this.apiData.siteOfficeId) : 0
    );
    formData.append('data.ModuleId', this.apiData?.moduleId);
    formData.append('data.SubModuleId', this.apiData?.subModuleId);
    formData.append('data.CategoryId', this.apiData?.categoryId);
    formData.append('data.DropdownTypeId', this.apiData?.dropdownTypeId);
    formData.append('data.file', file);
    formData.append(
      'data.DataCapturingSlotGroupId',
      this.apiData.dataCapturingSlotGroupId
    );
    formData.append('message', null);
    this.ghgService
      .uploadRowExcelTemplate(this.apiData?.categoryId, formData)
      .subscribe((res: any) => {
        if (!res.data) this.messageService.errorToast(res.errorMessage);
        else {
          this.messageService.successToast('File uploaded successfully');
          this.isRowExcelUploaded.emit();
        }
        this.spinner.hide();
      });
    event.target.value = '';
  }

  isDataChange(index: any) {
    const row: any = this.getFormRow(index);
    row?.controls?.isDataChanged.setValue(true);
  }
}
