import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import Chart from 'chart.js/auto';
import ChartDataLabels from 'chartjs-plugin-datalabels';
import { NgxSpinner, NgxSpinnerService } from 'ngx-spinner';
import { MessageService } from 'src/app/services/message.service';
import { UserManagementService } from 'src/app/services/user-management.service';

@Component({
  selector: 'app-risk-exposure',
  templateUrl: './risk-exposure.component.html',
  styleUrls: ['./risk-exposure.component.scss'],
})
export class RiskExposureComponent implements OnInit {
  @ViewChild('dognutChart') dognutChart!: ElementRef<any>;
  chart: any;
  totalSupplierApplication = 0;
  riskExposuredata: any = {
    approvedApplication: 0,
    deniedApplication: 0,
    noOfHighRiskSuppliers: 0,
    noOfLowRiskSuppliers: 0,
    noOfMediumRiskSuppliers: 0,
    overallOrganizationRisk: 'N/A',
    pendingApplication: 0,
  };
  constructor(
    private _userMgtService: UserManagementService,
    private _msgService: MessageService,
    private _spinner: NgxSpinnerService
  ) {}

  ngOnInit(): void {
    this.GetDashboardRiskExposure();
  }

  GetDashboardRiskExposure() {
    this._spinner.show();
    this._userMgtService.getDashboardRiskExposure().subscribe((res: any) => {
      this._spinner.hide();
      if (res.data) {
        this.Chart();
        this.riskExposuredata = res.data[0];
        this.riskExposuredata.noOfLowRiskSuppliers == 0
          ? (this.riskExposuredata.noOfLowRiskSuppliers = null)
          : this.riskExposuredata.noOfLowRiskSupp,
          this.riskExposuredata.noOfMediumRiskSuppliers == 0
            ? (this.riskExposuredata.noOfMediumRiskSuppliers = null)
            : this.riskExposuredata.noOfMediumRiskSuppliers,
          this.riskExposuredata.noOfHighRiskSuppliers == 0
            ? (this.riskExposuredata.noOfHighRiskSuppliers = null)
            : this.riskExposuredata.noOfHighRiskSuppliers,
          (this.totalSupplierApplication =
            this.riskExposuredata.pendingApplication +
            this.riskExposuredata.approvedApplication +
            this.riskExposuredata.deniedApplication);
      } else {
        this._msgService.errorToast(res.errorMessage);
      }
    });
  }
  Chart() {
    setTimeout(() => {
      this.chart = new Chart(this.dognutChart.nativeElement, {
        type: 'doughnut',
        data: {
          datasets: [
            {
              label: 'Total Company',
              data: [
                this.riskExposuredata.noOfLowRiskSuppliers,
                this.riskExposuredata.noOfMediumRiskSuppliers,
                this.riskExposuredata.noOfHighRiskSuppliers,
              ],

              backgroundColor: ['#AFDF92', '#9BCDFF', '#FFA1B7'],
            },
          ],
          labels: ['Low Risk', 'Medium Risk', 'High Risk'],
        },

        plugins: [ChartDataLabels],
        options: {
          plugins: {
            datalabels: {
              color: 'white',
              font: {
                size: 20,
              },
              formatter: function (value, context) {
                return value;
              },
            },
            legend: {
              position: 'bottom',
              labels: {
                padding: 10,
              },
            },
          },
        },
      });
    }, 0);
  }
}
