import { ActivatedRoute } from '@angular/router';
import { Component, ElementRef, ViewChild, OnInit } from '@angular/core';
import { PowerbiReportService } from 'src/app/services/powerbi-report.service';
import { HttpClient } from '@angular/common/http';
import * as pbi from 'powerbi-client';
import {
  IReportEmbedConfiguration,
  models,
  Page,
  Report,
  service,
  Embed,
} from 'powerbi-client';
import { PowerBIReportEmbedComponent } from 'powerbi-client-angular';
import { NgxSpinnerService } from 'ngx-spinner';
import { NavigationExtras, Router } from '@angular/router';

@Component({
  selector: 'app-reports',
  templateUrl: './reports.component.html',
  styleUrls: ['./reports.component.scss'],
})
export class ReportsComponent {
  embedConfig: any;
  @ViewChild(PowerBIReportEmbedComponent)
  reportObj!: PowerBIReportEmbedComponent;
  @ViewChild('reportContainer') reportContainer!: any;

  isEmbedded = false;
  isScopOneReport = false;
  isScopTwoReport = false;
  isCombinedReport = false;
  powerbi: any;
  reportType: any;
  error: any;
  isvalid!: boolean;
  scopeName: any;
  workspace: string | undefined;
  constructor(
    private activatedRoute: ActivatedRoute,
    private powerbiService: PowerbiReportService,
    private spinner: NgxSpinnerService,
    private router: Router
  ) {
    const navigation = window.history.state;
    if (navigation && navigation.data) {
      if (navigation.data.key.name == 'GHG Emission Scope 1') {
        this.scopeName = 'Emission Scope 1';
        this.reportType = 'scope1';
      }
      if (navigation.data.key.name == 'GHG Emission Scope 2') {
        this.scopeName = 'Emission Scope 2';
        this.reportType = 'scope2';
      }
      if (navigation.data.key.name == 'GHG Emission All Scope') {
        this.scopeName = 'Emission All Scope';
        this.reportType = 'combined';
      }
      if (navigation.data.key.name == 'GHG Emission Scope 3') {
        this.scopeName = 'Emission Scope 3';
        this.reportType = 'scope3';
      }
      if (navigation.data.key.name == 'GHG Avoidance/ Carbon Offsetting') {
        this.scopeName = 'Avoidance/ Carbon Offsetting';
        this.reportType = 'GHGAvoidance-Scope1';
      }
      this.spinner.show();
      if (this.scopeName != undefined) {
        this.powerbiService.getEmbedReport(this.reportType).subscribe(
          (baseResponse: any) => {
            if (baseResponse.data != undefined && baseResponse.data != null) {
              // this.isScopOneReport = true;
              // this.isScopTwoReport = false;
              // this.isCombinedReport = false;
              this.spinner.hide();
              console.log(baseResponse);
              this.reportContainer = {
                type: 'report',
                tokenType: pbi.models.TokenType.Embed,
                accessToken: baseResponse.data.token,
                webUrl: baseResponse.data.embedUrl,
                id: baseResponse.data.id,
                permissions: pbi.models.Permissions.All,
                viewMode: pbi.models.ViewMode.View,
                settings: {
                  filterPaneEnabled: true,
                  navContentPaneEnabled: true,
                  layoutType: pbi.models.LayoutType.Custom,
                  customLayout: {
                    displayOption: pbi.models.DisplayOption.FitToWidth,
                  },
                },
              };
              console.log(this.reportContainer);
              this.isvalid = true;
            } else {
              this.isvalid = true;
              this.spinner.hide();
              console.log(baseResponse);
            }
          },
          (error: any) => {
            this.isScopTwoReport = false;
            this.spinner.hide();
            console.log(error);
          }
        );
      } else {
        this.spinner.hide();
        this.isvalid = true;
      }
      console.log(navigation.data);
    }
  }
}
