  ngOnInit(): void {
    // this.getSupplierDetails();
    if (this.supplierdDetails.photo) {
      this.imageUrl =
        environment.apiHostName + '/' + this.supplierdDetails.photo;
    }

    this.chart();
  }

  getSupplierDetails() {
    this.spinner.show();
    this._supplierService.getSupplierInfo(this.supplierId).subscribe(
      (res: any) => {
        this.supplierdDetails = res.data[0];
        this.spinner.hide();

        if (res.data[0].photo) {
          this.imageUrl = environment.apiHostName + '/' + res.data[0].photo;
        }
        setTimeout(() => {
          this.chart();
        }, 0);
      },
      (err: any) => {
        this.spinner.hide();
      }
    );
  }

  chart() {
    setTimeout(() => {
      const textAssign = (res: any) => {
        if (this.supplierdDetails.riskScore >= 70) {
          const data: any = {
            text: `Low Risk`,
            percentage: this.supplierdDetails.riskScore,
            color: '#9EC97F',
          };

          return data;
        } else if (
          this.supplierdDetails.riskScore >= 50 &&
          this.supplierdDetails.riskScore < 70
        ) {
          const data: any = {
            text: `Medium Risk`,
            percentage: this.supplierdDetails.riskScore,
            color: '#F3C96B',
          };
          return data;
        } else {
          const data: any = {
            text: `High Risk`,
            percentage: this.supplierdDetails.riskScore,
            color: '#F3001C',
          };
          return data;
        }
      };

      const ctx = document.getElementById('myChart');
      let selectedDatasetIndex = 0,
        selectedIndex = 0;
      const getVisibleDataItem = function (chart: any, metadata: any) {
        const data = metadata.data;
        for (let i = 0; i < data.length; i++) {
          if (chart.getDataVisibility(i)) {
            return i;
          }
        }
        return NaN;
      };
      // const showPercentage = function (chart: any) {
      //   const ctx = chart.ctx;
      //   const { width, height, top } = chart.chartArea;
      //   const centerX = width / 2;
      //   const centerY = height / 2 + top;
      //   ctx.save();
      //   ctx.font = 'bolder 20px Arial';
      //   const metrics = ctx.measureText('100.0%');
      //   ctx.clearRect(
      //     centerX - metrics.width / 2,
      //     centerY - 16,
      //     metrics.width,
      //     64,
      //   );
      //   const metadata = chart.getDatasetMeta(selectedDatasetIndex);
      //   if (!chart.getDataVisibility(selectedIndex)) {
      //     const index = getVisibleDataItem(chart, metadata);
      //     if (isNaN(index)) {
      //       return ctx.restore();
      //     }
      //     selectedIndex = index;
      //   }
      //   const metadataItem = metadata.data[selectedIndex];
      //   const sum = metadata.total;
      //   const value =
      //     chart.config.data.datasets[selectedDatasetIndex].data[selectedIndex];
      //   const percentage = (value / sum) * 100;
      //   const text = percentage.toFixed(1) + '%';
      //   ctx.fillStyle = metadataItem.options.backgroundColor;
      //   ctx.textBaseline = 'bottom';
      //   ctx.textAlign = 'center';
      //   ctx.fillText(text, centerX, centerY);
      //   ctx.textBaseline = 'top';
      //   ctx.fillText('Completed', centerX, centerY);
      //   ctx.restore();
      // };

      const showPercentage = function (chart: any) {
        const ctx = chart.ctx;
        const { width, height, top } = chart.chartArea;
        const centerX = width / 2;
        const centerY = height / 2 + top;
        ctx.save();
        ctx.font = '1.389vw Arial';
        const metrics = ctx.measureText('100.0%');
        ctx.clearRect(
          centerX - metrics.width / 2,
          centerY - 16,
          metrics.width,
          64
        );
        const metadata = chart.getDatasetMeta(selectedDatasetIndex);
        if (!chart.getDataVisibility(selectedIndex)) {
          const index = getVisibleDataItem(chart, metadata);
          if (isNaN(index)) {
            return ctx.restore();
          }
          selectedIndex = index;
        }
        const metadataItem = metadata.data[selectedIndex];
        const sum = metadata.total;
        const value =
          chart.config.data.datasets[selectedDatasetIndex].data[selectedIndex];
        const percentage = (value / sum) * 100;
        const text = percentage.toFixed(1) + '%';

        // Set the color for the percentage text
        ctx.fillStyle = 'black'; // Change this to the desired color

        ctx.textBaseline = 'bottom';
        ctx.textAlign = 'center';
        ctx.fillText(text, centerX, centerY);

        // Set the color for the other text
        ctx.fillStyle = '#015E89'; // Change this to the desired color

        ctx.textBaseline = 'top';

        ctx.fillText(textAssign('').text, centerX, centerY);
        ctx.restore();
      };

      const pluginShowPercentage = {
        id: 'showPercentage',
        afterDraw: function (chart: any) {
          if (selectedDatasetIndex >= 0) {
            showPercentage(chart);
          }
        },
      };
      const myChart = new Chart(this.donutCanvas.nativeElement, {
        type: 'doughnut',
        plugins: [pluginShowPercentage],
        data: {
          // labels: ['Jan', 'Feb', 'Mar', 'Apr', 'May'],
          datasets: [
            {
              data: [
                this.supplierdDetails?.riskScore,
                100 - parseInt(this.supplierdDetails.riskScore),
              ],
              backgroundColor: [textAssign('').color, '#5971C0'],
            },
          ],
        },
        options: {
          // cutout: 140,
          // responsive: true,
          // aspectRatio: 1,
          // maintainAspectRatio: true,

          cutout: '70%', // Adjust inner radius to create a doughnut hole
          responsive: false, // Disable responsiveness to maintain fixed height
          maintainAspectRatio: false,

          onClick(e, elements, chart) {
            if (elements.length > 0) {
              const { index, datasetIndex } = elements[0];
              selectedDatasetIndex = datasetIndex;
              selectedIndex = index;
              chart.draw();
            }
          },
          plugins: {
            // tooltip: false
          },
        },
      });
    }, 500);
  }

  updateApplicationStatus(applicationID: Number) {
    this.dialogRef = this.dialog.open(ConfirmationDialogComponent, {
      width: '24.514vw',
      height: '12.083vw',
      data: {
        header: applicationID == 2 ? 'Approve ?' : 'Exercise Discretion ?',
        data: { bodyData: `Are you sure, you want to submit the changes?` },
        button: {
          isSubmit: true,
          isCancel: true,
          isDelete: false,
        },
      },
    });
    this.dialogRef.disableClose = true;

    this.dialogRef.afterClosed().subscribe((result: any) => {
      if (result == 'ok') {
        this.spinner.show();
        console.log(this.supplierId, applicationID);

        this._supplierService
          .updateSupplierApplicationStatus(this.supplierId, applicationID)
          .subscribe((res: any) => {
            this.spinner.hide();
            if (res.data) {
              this.message.successToast(
                'Application status changes successfully'
              );
              this.router.navigate(['/supplier_assessment']);
            } else {
              this.message.errorToast(res.errorMessage);
            }
          });
      }
    });
  }
