import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { URLS } from 'src/app/app-url.constant';
import { environment } from 'src/environments/environment';
import { CommonService } from '../common/common.service';

@Injectable({
  providedIn: 'root',
})
export class SupplierAssessmentService {
  constructor(
    private http: HttpClient,
    private commonSvc: CommonService
  ) {}

  getSupplierStatusList() {
    return this.http.get(
      `${URLS.baseApiHostUrl}${URLS.Supplier.endpoint}${URLS.Supplier.methods.GetAllSupplierApplicationStatusMaster}`
    );
  }

  getAllSupplierTypeMaster() {
    return this.http.get(
      `${URLS.baseApiHostUrl}${URLS.Supplier.endpoint}${URLS.Supplier.methods.GetAllSupplierTypeMaster}`
    );
  }

  getSupplierList() {
    return this.http.get(
      `${URLS.baseApiHostUrl}${URLS.Supplier.endpoint}${URLS.Supplier.methods.GetAllSupplierList}`
    );
  }

  getQuestionerCategoryList() {
    return this.http.get(
      `${URLS.baseApiHostUrl}${URLS.Supplier.endpoint}${URLS.Supplier.methods.GetAllSupplierQuestionCategoryMaster}`
    );
  }

  getAllQuestionList() {
    return this.http.get(
      `${URLS.baseApiHostUrl}${URLS.Supplier.endpoint}${URLS.Supplier.methods.GetAllSupplierQuestionMaster}`
    );
  }

  getSupplierInfo(supplierId: number) {
    return this.http.get(
      `${URLS.baseApiHostUrl}${URLS.Supplier.endpoint}${URLS.Supplier.methods.GetSupplierListById}?Id=${supplierId}`
    );
  }

  getSupplierSavedQuestion(supplierId: number) {
    return this.http.get(
      `${URLS.baseApiHostUrl}${URLS.Supplier.endpoint}${URLS.Supplier.methods.GetSupplierAllQuestionTxn}?SupplierId=${supplierId}`
    );
  }

  SaveSupplierDetails(form: any) {
    const requestBody = {
      requestUserLoginName: JSON.parse(localStorage.getItem('userData')!)
        .username,
      requestId: '',
      data: {
        supplierTypeId: form.userType,
        actionOwnerId: form.actionOwner,
        actionReviewerId: form.actionReviewer,
        legalName: form.name,
        countryId: form.country,
        stateId: form.state,
        // cityId: form.city,
        originId: form.city,
        email: form.email,
        mobile: form.contactNumber,
        photo: form.profile,
        applicationStatusId: null,
        details: '',
        riskScore: 0,
      },
    };

    return this.http.post(
      `${URLS.baseApiHostUrl}${URLS.Supplier.endpoint}${URLS.Supplier.methods.AddSupplierlist}`,
      requestBody
    );
  }

  saveQuestioner(questionArray: []) {
    return this.http.put(
      `${URLS.baseApiHostUrl}${URLS.Supplier.endpoint}${URLS.Supplier.methods.UpdateSupplierQuestionAnswer}`,
      questionArray
    );
  }

  updateSupplierApplicationStatus(supplierId: Number, applicationID: Number) {
    const requestBody = {
      requestUserLoginName: this.commonSvc.getReqLoginUserName(),
      requestId: '',
      data: {
        supplierId: supplierId,
        applicationStatusId: applicationID,
      },
    };
    return this.http.post(
      `${URLS.baseApiHostUrl}${URLS.Supplier.endpoint}${URLS.Supplier.methods.UpdateSupplierApplicationStatus}`,
      requestBody
    );
  }
  updateSupplierdetails(
    supplierid: Number,
    suplierdetails: any,
    oldprofile: any
  ) {
    const requestBody = {
      id: supplierid,
      supplierTypeId: suplierdetails.userType,
      supplierType: '',
      actionOwnerId: suplierdetails.actionOwner,
      actionReviewerId: suplierdetails.actionReviewer,
      legalName: suplierdetails.name,
      originId: suplierdetails.city,
      email: suplierdetails.email,
      mobile: suplierdetails.contactNumber,
      photo: oldprofile,
      photoNew: suplierdetails.profile,
      countryId: suplierdetails.country,
      stateId: suplierdetails.state,
      // riskScore: 0,
      // applicationStatusId: 0,
      // isAnswerSubmitted: true,
    };
    return this.http.put(
      `${URLS.baseApiHostUrl}${URLS.Supplier.endpoint}${URLS.Supplier.methods.UpdateSupplierlist}?userLoginName=${this.commonSvc.getReqLoginUserName()}`,
      requestBody
    );
  }

  checkMailExist(email: string) {
    console.log('pp');

    const requestBody = {
      requestUserLoginName: this.commonSvc.getReqLoginUserName(),
      requestId: '',
      data: email,
    };
    return this.http.post(
      `${URLS.baseApiHostUrl}${URLS.Supplier.endpoint}${URLS.Supplier.methods.IsAnExistingEmail}`,
      requestBody
    );
  }

  checkMobileExist(mobileNo: Number) {
    console.log('ser');

    const requestBody = {
      requestUserLoginName: this.commonSvc.getReqLoginUserName(),
      requestId: '',
      data: mobileNo,
    };
    return this.http.post(
      `${URLS.baseApiHostUrl}${URLS.Supplier.endpoint}${URLS.Supplier.methods.IsAnExistingContact}`,
      requestBody
    );
  }
}
