import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { URLS } from 'src/app/app-url.constant';
import { CommonService } from '../common/common.service';
import * as moment from 'moment';
import { of } from 'rxjs';
import { DatePipe } from '@angular/common';
import { NumberInput } from '@angular/cdk/coercion';

@Injectable({
  providedIn: 'root',
})
export class GhgAvoidanceService {
  constructor(
    private _http: HttpClient,
    private commonSvc: CommonService,
    private _datepipe: DatePipe
  ) {}

  GetAllGhgavoidanceCategory() {
    return this._http.post(
      `${URLS.baseApiHostUrl}${URLS.GHGAvoidance.endpoint}${URLS.GHGAvoidance.methods.GetAllGhgavoidanceCategory}`,
      {}
    );
  }

  GetGhgavoidanceDropdowntype(avoidanceCategoryId: any) {
    const requestBody = {
      requestUserLoginName: this.commonSvc.getReqLoginUserName(),
      requestId: '',
      data: {
        avoidanceCategoryId: avoidanceCategoryId,
      },
    };

    return this._http.post(
      `${URLS.baseApiHostUrl}${URLS.GHGAvoidance.endpoint}${URLS.GHGAvoidance.methods.GetGhgavoidanceDropdowntype}`,
      requestBody
    );
  }

  GetAllGHGAvoidanceDisplacementConfig() {
    return this._http.get(
      `${URLS.baseApiHostUrl}${URLS.GHGAvoidance.endpoint}${URLS.GHGAvoidance.methods.GetAllGHGAvoidanceDisplacementConfig}`
    );
  }

  GetAllGHGAvoidanceFuel() {
    return this._http.get(
      `${URLS.baseApiHostUrl}${URLS.GHGAvoidance.endpoint}${URLS.GHGAvoidance.methods.GetAllGHGAvoidanceFuel}`
    );
  }
  GetAllGHGAvoidanceFuelType(id: any) {
    const requestBody = {
      requestUserLoginName: this.commonSvc.getReqLoginUserName(),
      requestId: '',
      data: {
        ghgavoidanceFuelId: id,
      },
      message: 'string',
    };
    return this._http.post(
      `${URLS.baseApiHostUrl}${URLS.GHGAvoidance.endpoint}${URLS.GHGAvoidance.methods.GetAllGHGAvoidanceFuelType}`,
      requestBody
    );
  }

  AddGHGAvoidanceDisplacementConfig(form: any) {
    const reguestBody = {
      requestUserLoginName: this.commonSvc.getReqLoginUserName(),
      requestId: '',
      data: {
        effectiveDate: this._datepipe.transform(
          form.effectiveDate,
          'yyyy-MM-dd'
        ),
        dropdowntypeId: Number(form.material),
        addGHGAvoidanceDisplacementConfigDataDTOs: form.fuelTable,
      },
      message: '',
    };
    return this._http.post(
      `${URLS.baseApiHostUrl}${URLS.GHGAvoidance.endpoint}${URLS.GHGAvoidance.methods.AddGHGAvoidanceDisplacementConfig}`,
      reguestBody
    );
  }

  GetGHGAvoidanceDisplacementConfigByGroupId(id: number) {
    const reguestBody = {
      requestUserLoginName: this.commonSvc.getReqLoginUserName(),
      requestId: '',
      data: {
        groupId: id,
      },
      message: '',
    };

    return this._http.post(
      `${URLS.baseApiHostUrl}${URLS.GHGAvoidance.endpoint}${URLS.GHGAvoidance.methods.GetGHGAvoidanceDisplacementConfigByGroupId}`,
      reguestBody
    );
  }

  //get data according to date
  GetGHGItemsByDCFGroupId(requestBody: any) {
    return this._http.post(
      `${URLS.baseApiHostUrl}${URLS.GHGAvoidance.endpoint}${URLS.GHGAvoidance.methods.GetGHGItemsByDCFGroupId}`,
      requestBody
    );
  }

  deleteAccordionData(requestBody: any) {
    let options = {
      body: requestBody,
    };
    return this._http.delete(
      `${URLS.baseApiHostUrl}${URLS.GHGAvoidance.endpoint}${URLS.GHGAvoidance.methods.DeleteGHGItem}`,
      options
    );
  }
  GHGAvoidanceUnit() {
    return this._http.get(
      `${URLS.baseApiHostUrl}${URLS.GHGAvoidance.endpoint}${URLS.GHGAvoidance.methods.GHGAvoidanceUnit}`
    );
  }

  saveAndUpdateGhgData(requestbody: any) {
    return this._http.post(
      `${URLS.baseApiHostUrl}${URLS.GHGAvoidance.endpoint}${URLS.GHGAvoidance.methods.AddUpdateGHGItemsByDCFGroupId}`,
      requestbody
    );
  }

  GetGhgAvoidanceCalculation(
    reportingPeriodId: any,
    selectedMaterialId: any,
    officeData: any
  ) {
    const requestBody = {
      requestUserLoginName: this.commonSvc.getReqLoginUserName(),
      requestId: '',
      data: {
        ghgavoidanceDropdownTypeId: selectedMaterialId,
        dataCapturingSlotGroupId: reportingPeriodId,
        corporateOfficeId: officeData.corporateOfficeId,
        regionalOfficeId: officeData.regionalOfficeId,
        siteOfficeId: officeData.siteOfficeId,
      },
      message: 'string',
    };
    return this._http.post(
      `${URLS.baseApiHostUrl}${URLS.GHGAvoidance.endpoint}${URLS.GHGAvoidance.methods.GetGhgAvoidanceCalculation}`,
      requestBody
    );
  }

  getAvoidanceBatchHistory(requestBody: any) {
    return this._http.post(
      `${URLS.baseApiHostUrl}${URLS.GHGAvoidance.endpoint}${URLS.GHGAvoidance.methods.GHGAvoidanceDataBatchHistoryByBatchId}`,
      requestBody
    );
  }
}
