import { Component, ElementRef, ViewChild, OnInit } from '@angular/core';
import { PowerbiReportService } from 'src/app/services/powerbi-report.service';
import { HttpClient } from '@angular/common/http';
import * as pbi from 'powerbi-client';
import {
  IReportEmbedConfiguration,
  models,
  Page,
  Report,
  service,
  Embed,
} from 'powerbi-client';
import { PowerBIReportEmbedComponent } from 'powerbi-client-angular';
import { NgxSpinnerService } from 'ngx-spinner';
import { NavigationExtras, Router } from '@angular/router';

@Component({
  selector: 'app-analysis-reporting',
  templateUrl: './analysis-reporting.component.html',
  styleUrls: ['./analysis-reporting.component.scss'],
})
export class AnalysisReportingComponent {
  embedConfig: any;
  @ViewChild(PowerBIReportEmbedComponent)
  reportObj!: PowerBIReportEmbedComponent;
  @ViewChild('reportContainer') reportContainer!: any;

  reportsArray: any = [
    {
      name: 'GHG Emission All Scope',
      routerLink: '/reports',
      icon: '../../../../assets/images/reporting/ghg_icon.png',
    },
    {
      name: 'GHG Emission Scope 1',
      routerLink: '/reports',
      icon: '../../../../assets/images/reporting/ghg_scope1_icon.png',
    },
    {
      name: 'GHG Emission Scope 2',
      routerLink: '/reports',
      icon: '../../../../assets/images/reporting/ghg_scope2_icon.png',
    },
    {
      name: 'GHG Emission Scope 3',
      routerLink: '/reports',
      icon: '../../../../assets/images/reporting/ghg_scope3_icon.png',
    },
    {
      name: 'GHG Avoidance/ Carbon Offsetting',
      routerLink: '/reports',
      icon: '../../../../assets/images/reporting/ghg_scope1_icon.png',
    },
  ];

  isEmbedded = false;
  isScopOneReport = false;
  isScopTwoReport = false;
  isCombinedReport = false;
  powerbi: any;
  reportType: any;
  error: any;
  isvalid!: boolean;
  workspace: string | undefined;
  dynamicDivHeight: string = '700px';

  constructor(
    private powerbiService: PowerbiReportService,
    private spinner: NgxSpinnerService,
    private router: Router
  ) {}

  ngOnInit() {}
  EmbedReport() {
    this.Embedreport();
  }
  Embedreport() {
    this.spinner.show();
    this.reportType = 'scope1';
    this.powerbiService.getEmbedReport(this.reportType).subscribe(
      (baseResponse: any) => {
        if (baseResponse != undefined) {
          this.isScopOneReport = true;
          this.isScopTwoReport = false;
          this.isCombinedReport = false;
          this.spinner.hide();
          console.log(baseResponse);
          this.reportContainer = {
            type: 'report',
            tokenType: pbi.models.TokenType.Embed,
            accessToken: baseResponse.data.token,
            webUrl: baseResponse.data.embedUrl,
            id: baseResponse.data.id,
            permissions: pbi.models.Permissions.All,
            viewMode: pbi.models.ViewMode.View,
            settings: {
              filterPaneEnabled: true,
              navContentPaneEnabled: true,
              layoutType: pbi.models.LayoutType.Custom,
              customLayout: {
                displayOption: pbi.models.DisplayOption.FitToWidth,
              },
            },
          };
          console.log(this.reportContainer);
          this.isvalid = true;
        } else {
          console.log(baseResponse);
        }
      },
      (error: any) => {
        this.isScopTwoReport = false;
        this.spinner.hide();
        console.log(error);
      }
    );
  }
  CombinedReport() {
    this.spinner.show();
    this.reportType = 'combined';
    this.powerbiService.getEmbedReport(this.reportType).subscribe(
      (baseResponse: any) => {
        if (baseResponse != undefined) {
          this.isScopOneReport = false;
          this.isScopTwoReport = false;
          this.isCombinedReport = true;
          this.spinner.hide();
          console.log(baseResponse);
          this.reportContainer = {
            type: 'report',
            tokenType: pbi.models.TokenType.Embed,
            accessToken: baseResponse.data.token,
            webUrl: baseResponse.data.embedUrl,
            id: baseResponse.data.id,
            permissions: pbi.models.Permissions.All,
            viewMode: pbi.models.ViewMode.View,
            settings: {
              filterPaneEnabled: true,
              navContentPaneEnabled: true,
              layoutType: pbi.models.LayoutType.Custom,
              customLayout: {
                displayOption: pbi.models.DisplayOption.FitToWidth,
              },
            },
          };
          console.log(this.reportContainer);
          this.isvalid = true;
        } else {
          console.log(baseResponse);
        }
      },
      (error: any) => {
        this.isScopTwoReport = false;
        this.spinner.hide();
        console.log(error);
      }
    );
  }
  EmbedScope2() {
    this.spinner.show();
    this.reportType = 'scope2';
    this.powerbiService.getEmbedReport(this.reportType).subscribe(
      (baseResponse: any) => {
        if (baseResponse != undefined) {
          this.isScopOneReport = false;
          this.isScopTwoReport = true;
          this.isCombinedReport = false;
          this.spinner.hide();
          console.log(baseResponse);
          this.reportContainer = {
            type: 'report',
            tokenType: pbi.models.TokenType.Embed,
            accessToken: baseResponse.data.token,
            webUrl: baseResponse.data.embedUrl,
            id: baseResponse.data.id,
            permissions: pbi.models.Permissions.All,
            viewMode: pbi.models.ViewMode.View,
            settings: {
              filterPaneEnabled: true,
              navContentPaneEnabled: true,
              layoutType: pbi.models.LayoutType.Custom,
              customLayout: {
                displayOption: pbi.models.DisplayOption.FitToWidth,
              },
            },
          };
          console.log(this.reportContainer);
          this.isvalid = true;
        } else {
          console.log(baseResponse);
        }
      },
      (error: any) => {
        this.isScopOneReport = false;
        this.spinner.hide();
        console.log(error);
      }
    );
  }

  navigate(data: any) {
    let navigationExtras: NavigationExtras = {
      state: {
        data: {
          key: data, // Your data object
        },
      },
    };
    if (data?.routerLink)
      this.router.navigate([data.routerLink], navigationExtras);
  }
}
