import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { commonData } from 'src/app/components/shared/commonData';
import { SupplierAssessmentService } from 'src/app/services/manager/supplier-assessment.service';
import { MessageService } from 'src/app/services/message.service';
import { OrganizationSetupService } from 'src/app/services/organization-setup.service';
import { UserManagementService } from 'src/app/services/user-management.service';

@Component({
  selector: 'app-supplier-assessment-suit',
  templateUrl: './supplier-assessment-suit.component.html',
  styleUrls: ['./supplier-assessment-suit.component.scss'],
})
export class SupplierAssessmentSuitComponent implements OnInit {
  dataSearch!: any;
  isTab1Enable: boolean = true;
  isTab2Enable!: boolean;
  supplierList: any = [];
  supplierCloneList: any = [];
  userTypeList: any = [];
  locationList: any = [];
  supplierStatus: any = [];
  currentPage = 0;
  pageSize = 0;
  Math: any = Math;
  supplierListLength: any;

  constructor(
    private supplierService: SupplierAssessmentService,
    private organizationService: OrganizationSetupService,
    private userManagementService: UserManagementService,
    private spinner: NgxSpinnerService,
    private messageService: MessageService,
    private router: Router,
    private _commonData: commonData
  ) {}

  ngOnInit(): void {
    this.getAllUserType();
    this.getLocation();
    this.getStatusList();
    this.getSupplierList();
    this.pageSize = this._commonData.pageSize;
    this.currentPage = this._commonData.currentPage;
  }

  filterProducts(): void {
    this.currentPage = 1;
    if (this.dataSearch.trim() !== '') {
      this.supplierList = this.supplierCloneList?.filter((product: any) => {
        if (
          product?.legalName
            ?.toLowerCase()
            ?.includes(this.dataSearch?.toLowerCase())
        )
          return product?.legalName
            ?.toLowerCase()
            ?.includes(this.dataSearch?.toLowerCase());
        else if (
          product?.email?.toLowerCase().includes(this.dataSearch?.toLowerCase())
        )
          return product?.email
            ?.toLowerCase()
            ?.includes(this.dataSearch?.toLowerCase());
        else if (
          product?.mobile
            ?.toLowerCase()
            ?.includes(this.dataSearch?.toLowerCase())
        )
          return product?.mobile
            ?.toLowerCase()
            ?.includes(this.dataSearch?.toLowerCase());
        // else if (product?.riskScore?.matches(this.dataSearch))
        //   return product?.riskScore?.matches(this.dataSearch);
      });
      this.supplierListLength = this.supplierList?.length;
    } else {
      // If the search term is empty, show all products
      this.supplierList = this.supplierCloneList;
      this.supplierListLength = this.supplierList?.length;
    }
  }

  // redirectTo(path: string, id: number) {
  //   this.router.navigate([path, id]);
  // }

  getAllUserType() {
    this.organizationService.getAllUserType().subscribe((res: any) => {
      this.userTypeList = res.data;
    });
  }

  getLocation() {
    this.spinner.show();
    this.userManagementService.getLocation().subscribe((res: any) => {
      this.locationList = res.data;
      this.spinner.hide();
    });
  }

  getStatusList() {
    this.spinner.show();
    this.supplierService.getSupplierStatusList().subscribe((res: any) => {
      this.supplierStatus = res.data;
      this.spinner.hide();
    });
  }

  getSupplierList() {
    this.spinner.show();
    this.supplierService.getSupplierList().subscribe((res: any) => {
      this.supplierList = res.data;
      this.supplierListLength = res.data.length;
      this.supplierCloneList = res.data;
      this.spinner.hide();
    });
  }
}
