import { NgModule } from '@angular/core';
import { CommonModule, DecimalPipe } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
// import { authGuard } from 'src/app/route/auth.guard';

import { DashboardComponent } from './dashboard/dashboard.component';
import { SharedModule } from '../shared/shared.module';
import { GhgEmissionComponent } from './ghg-emission/ghg-emission.component';
import { FuelTableComponent } from './ghg-emission/fuel-table/fuel-table.component';
import { SupplierAssessmentSuitComponent } from './supplier-assessment-suit/SupplierAssesment-list/supplier-assessment-suit.component';
import { AddEditSupplierComponent } from './supplier-assessment-suit/add-edit-supplier/add-edit-supplier.component';
import { SupplierRiskExposureComponent } from './supplier-assessment-suit/supplier-risk-exposure/supplier-risk-exposure.component';
import { SupplierQuestionerComponent } from './supplier-assessment-suit/supplier-questioner/supplier-questioner.component';
import { RiskExposureComponent } from './supplier-assessment-suit/risk-exposure/risk-exposure.component';
import { authGuard } from 'src/app/guards/auth.guard';

import { AnalysisReportingComponent } from './analysis-reporting/analysis-reporting.component';
import { LocationSelectComponent } from './ghg-emission/location-select/location-select.component';
import { ReportsComponent } from './analysis-reporting/reports/reports.component';
import { CreateSupplierComponent } from './ghg-emission/create-supplier/create-supplier.component';
import { GhgAvoidanceComponent } from './ghg-avoidance/ghg-avoidance.component';
import { InputTableComponent } from './ghg-avoidance/input-table/input-table.component';
import { GhgAvoidanceCalculationComponent } from './ghg-avoidance/ghg-avoidance-calculation/ghg-avoidance-calculation.component';
import { Scope3ChildComponent } from './ghg-emission/scope3-child/scope3-child.component';
import { LocationSelectionAvoidanceComponent } from './ghg-avoidance/location-selection-avoidance/location-selection-avoidance.component';
import { DisplacementConfigurationComponent } from './ghg-avoidance/displacement-configuration/displacement-configuration.component';

const routes: Routes = [
  {
    path: 'dashboard',
    component: DashboardComponent,
    data: { role: ['Supervisor', 'Operator', 'Manager'] },
    canActivate: [authGuard],
  },
  {
    path: 'location_selection',
    data: { role: ['Supervisor', 'Operator', 'Manager'] },
    component: LocationSelectComponent,
    canActivate: [authGuard],
  },
  {
    path: 'GHG_Emission',
    component: GhgEmissionComponent,
    data: { role: ['Supervisor', 'Operator', 'Manager'] },
    canActivate: [authGuard],
  },
  {
    path: 'supplier_assessment',
    component: SupplierAssessmentSuitComponent,
    data: { role: ['Supervisor', 'Operator', 'Manager'] },
    canActivate: [authGuard],
  },
  {
    path: 'add_supplier',
    component: AddEditSupplierComponent,
    data: { role: ['Supervisor', 'Operator', 'Manager'] },
    canActivate: [authGuard],
  },
  {
    path: 'edit_supplier/:id',
    component: AddEditSupplierComponent,
    data: { role: ['Supervisor', 'Operator', 'Manager'] },
    canActivate: [authGuard],
  },

  {
    path: 'analysis-reporting',
    component: AnalysisReportingComponent,
    data: { role: ['Supervisor', 'Operator', 'Manager'] },
    canActivate: [authGuard],
  },
  {
    path: 'reports',
    component: ReportsComponent,
    data: { role: ['Supervisor', 'Operator', 'Manager'] },
    // canActivate: [authGuard],
  },
  {
    path: 'create-supplier',
    component: CreateSupplierComponent,
    data: { role: ['Supervisor', 'Operator', 'Manager'] },
  },
  {
    path: 'ghg-avoidance',
    component: GhgAvoidanceComponent,
  },
  {
    path: 'location-selection-Avoidance',
    component: LocationSelectionAvoidanceComponent,
  },
];

@NgModule({
  declarations: [
    DashboardComponent,
    GhgEmissionComponent,
    FuelTableComponent,
    SupplierAssessmentSuitComponent,
    AddEditSupplierComponent,
    SupplierRiskExposureComponent,
    SupplierQuestionerComponent,
    RiskExposureComponent,
    AnalysisReportingComponent,
    LocationSelectComponent,
    ReportsComponent,
    CreateSupplierComponent,
    GhgAvoidanceComponent,
    InputTableComponent,
    GhgAvoidanceCalculationComponent,
    Scope3ChildComponent,
    LocationSelectionAvoidanceComponent,
    DisplacementConfigurationComponent,
  ],
  imports: [CommonModule, SharedModule, RouterModule.forChild(routes)],
  providers: [DecimalPipe],
})
export class SupervisorModule {}
