  ngOnChanges(changes: SimpleChanges): void {
    this.materialType = '';
    // this.materialList = [];
    this.overAllTotal = 0;
    this.tableData = [];
    this.OriginalArray = [];
  }

  openAndClosePopover() {
    setTimeout(() => {
      this.popTemplate12.nativeElement.click();
      this.isPopOverActive1 = !this.isPopOverActive1;
    }, 0);
  }

  changeMaterialType() {
    this.overAllTotal = 0;
    this.OriginalArray = [];
    this.overAllTotal = 0;
    this.tableData = [];

    if (this.reportingPeriod) {
      if (this.isPopOverActive1) this.openAndClosePopover();
      this.getCalculation();
    } else {
      this.messageService.errorToast('Please select Reporting Period');
    }
  }

  getCalculation() {
    this.loader.show();
    this.ghgAvoidanceSvc
      .GetGhgAvoidanceCalculation(
        this.reportingPeriod,
        this.materialType,
        this.officeData
      )
      .subscribe(
        (res: any) => {
          if (res.data) {
            var array = res.data;
            this.OriginalArray = [...res.data];
            for (var i = 0; i < array.length; i++) {
              this.overAllTotal =
                this.overAllTotal +
                array[i].commercialSector +
                array[i].industrialSector +
                array[i].householdSector +
                array[i].transportSector;
              if (this.tableData.length == 0) {
                this.tableData.push({
                  ghgavoidanceFuelTypeId: array[i].ghgavoidanceFuelTypeId,
                  ghgavoidanceFuelId: array[i].ghgavoidanceFuelId,
                  ghgavoidanceEmissionTypeId:
                    array[i].ghgavoidanceEmissionTypeId,
                  ghgavoidanceFuelName: array[i].ghgavoidanceFuelName,
                  ghgavoidanceFuelTypeName: array[i].ghgavoidanceFuelTypeName,
                  ghgavoidanceEmissionTypeName:
                    array[i].ghgavoidanceEmissionTypeName,
                  [array[i].ghgavoidanceEmissionTypeName]: {
                    industrialSector: array[i].industrialSector,
                    commercialSector: array[i].commercialSector,
                    transportSector: array[i].transportSector,
                    householdSector: array[i].householdSector,
                  },
                });
              } else {
                var index = this.tableData.findIndex((res: any) => {
                  return (
                    res.ghgavoidanceFuelId == array[i].ghgavoidanceFuelId &&
                    res.ghgavoidanceFuelTypeId ==
                      array[i].ghgavoidanceFuelTypeId
                  );
                });

                if (index == -1) {
                  this.tableData.push({
                    ghgavoidanceFuelTypeId: array[i].ghgavoidanceFuelTypeId,
                    ghgavoidanceFuelId: array[i].ghgavoidanceFuelId,
                    ghgavoidanceEmissionTypeId:
                      array[i].ghgavoidanceEmissionTypeId,
                    ghgavoidanceFuelName: array[i].ghgavoidanceFuelName,
                    ghgavoidanceFuelTypeName: array[i].ghgavoidanceFuelTypeName,
                    ghgavoidanceEmissionTypeName:
                      array[i].ghgavoidanceEmissionTypeName,
                    [array[i].ghgavoidanceEmissionTypeName]: {
                      industrialSector: array[i].industrialSector,
                      commercialSector: array[i].commercialSector,
                      transportSector: array[i].transportSector,
                      householdSector: array[i].householdSector,
                    },
                  });
                } else {
                  this.tableData[index][array[i].ghgavoidanceEmissionTypeName] =
                    {
                      industrialSector: array[i].industrialSector,
                      commercialSector: array[i].commercialSector,
                      transportSector: array[i].transportSector,
                      householdSector: array[i].householdSector,
                    };
                }
              }
            }
          } else {
            this.messageService.errorToast('No Data Found');
            this.OriginalArray = [];
            this.overAllTotal = 0;
            this.tableData = [];
          }
          this.loader.hide();
          console.log(this.tableData, 'tabledata');
        },
        (err: any) => {
          this.loader.hide();
          this.messageService.errorToast(err.errorMessage);
          this.OriginalArray = [];
          this.overAllTotal = 0;
          this.tableData = [];
        }
      );
  }

  getEmmissionTotal(emmissionType: any, sectorType: any) {
    var total: any = 0;
    this.tableData.forEach((res: any) => {
      if (res[emmissionType]) {
        if (res[emmissionType][sectorType]) {
          total = total + res[emmissionType][sectorType];
        }
      }
    });
    // total = total.toFixed(2);
    return this.tableData.length > 0 ? total : 0;
  }

  getSectorTotal(sectorType: any) {
    var total = 0;
    this.OriginalArray.forEach((res: any) => {
      // total = total + res[sectorType] ? res[sectorType] : 0;
      if (res[sectorType]) total = total + res[sectorType];
    });
    return this.tableData.length > 0 ? total : 0;
  }
